<template>
	<header class="header">
		<div class="inner">
			<h1 class="logo"><a href="./home/index.php" class="logo" title="广东网联实业有限公司"><span>广州·东莞·惠州·深圳</span></a></h1>
			<ul>
				<li>风格</li>
				<li>管理</li>
				<li>帮助</li>
				<li>客服</li>
			</ul>
			<div class="h_center g-fr">
				<span>会员中心</span>
				<!--router-link :to="preview">预览</router-link-->
				<input type="button" @click="gotoSaveCom" class="preview" value="保存" />
				<input type="button" @click="gotoPreview" class="preview" value="预览" />
			</div>
		</div>
	</header>
</template>
<script>
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	export default {
		name: 'headerNav',
		data() {
			return {
				preview: '/prev/' + this.$route.params.id + "/" + this.$route.params.param,
				id: this.$route.params.id,
				msg: 'header'
			}
		},
		created() {
			console.log(this.id)
		},
		methods: {
			gotoPreview: function() {
				this.setOrderId(this.id);
				this.previewData(this.id);
				console.log(this.$router)
				this.$router.push(this.preview);
				//window.open(this.preview);      
				//location.href = this.preview;
			},
			gotoSaveCom() {

				this.saveData(this.id);

				console.log(this.id)
			},
			...mapActions([
				'saveData'
			]),
			...mapMutations([
				'setOrderId', 'previewData'
			]),
		},

		computed: {
			...mapGetters(["state"])
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.header {
		height: 80px;
		background-color: #fff;
		margin-bottom: 50px;
		padding: 0 40px;
		box-shadow: 0 2px 10px 0px rgba(0, 0, 0, 0.15);
	}
	
	.h_center {
		padding-top: 20px;
		span {
			width: 100px;
			height: 35px;
			display: inline-block;
			line-height: 35px;
			margin: 0 0 0 20px;
			text-align: center;
			background: #fff;
			color: #777;
			border: 1px solid #d4d4d4;
			border-radius: 5px;
			cursor: pointer;
			transition: all 0.5s;
			-moz-transition: all 0.5s;
			-webkit-transition: all 0.5s;
			-o-transition: all 0.5s;
		}
		span:hover {
			background: #5874d8;
			color: #FFFFFF;
			border: 1px solid #5874d8;
		}
		.preview {
			width: 100px;
			height: 35px;
			display: inline-block;
			line-height: 35px;
			margin: 0 0 0 20px;
			text-align: center;
			background: #fff;
			color: #777;
			border: 1px solid #d4d4d4;
			border-radius: 5px;
			cursor: pointer;
			transition: all 0.5s;
			-moz-transition: all 0.5s;
			-webkit-transition: all 0.5s;
			-o-transition: all 0.5s;
		}
		.preview:hover {
			background: #5874d8;
			color: #FFFFFF;
			border: 1px solid #5874d8;
		}
	}
	
	.logo {
		float: left;
		line-height: 80px;
	}
	
	ul {
		float: left;
		li {
			float: left;
			width: 110px;
			font-weight: normal;
			border-right: 1px solid #E3E3E3;
			line-height: 80px;
			text-align: center;
			cursor: pointer;
		}
		li:hover {
			color: #5874d8;
		}
	}
</style>


