					<!--自由容器-->			
					<div v-if="i.tName=='freeContainer'" :class="['in_inner resizeMe',{'enter':i.isStreamColumn}]">
						<div :style="i.conStyle">
							<div v-for="(i,index) in i.itemInner" :itemMdata="i" :posCom="'free'+tIndex" :index="index" :is="i.tName"></div>
						</div>
					</div>
					<!--多列排版-->
					<div v-if="i.tName=='multiColumnContainer'" :class="['in_inner resizeMe multi_con g-cf', {'enter':i.isStreamColumn}]" :style="i.conStyle">
						<div v-for="it in i.column" :class="['clm',{'enter':i.isStreamColumn},'clm'+i.column]">
							<div class="clm_inner" :style="i.conStyle">
								<div class="area_conn">
									<template v-for="(i,index) in i.itemInner">
										<div v-if="i.column==it" :itemMdata="i" :posCom="'multi'" :index="index" :is="i.tName"></div>
									</template>
								</div>
							</div>
						</div>
					</div>
					<!--纵向标签-->
					<div v-if="i.tName=='longitudinalLabel'" :class="['in_inner']" :style="i.conStyle">
						<div class="g-cf longitud">
							<div class="tit_con">
								<ul>
									<li><span>标签1</span></li>
									<li><span>标签2</span></li>
								</ul>
							</div>
							<div class="long_con">
								<div class="edtem-label long_i1">
									标签1内容
								</div>
								<div class="edtem-label long_i2">
									标签2内容
								</div>
							</div>
						</div>

					</div>

					<!--通栏排版-->
					<div :style="i.layoutStyle" v-if="i.tName=='columnLayout'" :class="['in_inner columnLayout', {'enter':i.isStreamColumn}]">
						<div :style="i.layoutOuterStyle" :class="['in_inner resizeMe columnLayoutInner',{'enter':i.isStreamColumn}]">
							<div :style="i.conStyle" class="layout_inner">
								<div v-for="(i,index) in i.itemInner" :itemMdata="i" :posCom="'free'" :index="index" :is="i.tName"></div>
							</div>
						</div>
					</div>

				</div>
			</div>
		</div>
		<div class="pos_con">
			<div v-for="(i,index) in state.itemCon[state.delayed].posCom" class="g-cf in_outer">
				<div :itemMdata="i" :posCom="'posCom'" :index="index" :is="i.tName"></div>
			</div>
		</div>
	</div>
</template>

