<template>
	<div class="siteFormEdit">
		<div class="top">
			<p @click="oneshow" :class="{active:one_show}">常规</p>
			<p @click="twoshow" :class="{active:two_show}">高级</p>
		</div>
		<div class="one" v-show="one_show">
			<p class="name">
				<span>模块标题：</span>
				<input type="text" value="在线表单" />
			</p>
			<div class="show">
				<span>选项展示方式：</span>
				<ul>
					<li @click="showone">
						<div class="show_one"></div>
					</li>
					<li @click="showtwo">
						<div class="show_two"></div>
					</li>
				</ul>
			</div>
			<div class="arrange">
				<span>选项排列方式：</span>
				<ul>
					<li @click="arrangeone">
						<div class="arrange_one"></div>
					</li>
					<li @click="arrangetwo">
						<div class="arrange_two"></div>
					</li>
				</ul>
			</div>
			<div class="way">
				<span>提交后跳转方式：</span>
				<p @click="way_hid = false">
					<input type="radio" name="way" id="way1" checked="checked" />
					<label for="way1">默认</label>
				</p>
				<p @click="way_hid = true">
					<input type="radio" name="way" id="way2" checked="checked" />
					<label for="way2">自定义</label>
				</p>
				<div class="way_hid" v-show="way_hid">
					<p>
						<span>链接地址：</span>
						<input type="text" />
					</p>
					<button>设置链接</button>
				</div>
			</div>
		</div>
		<div class="two" v-show="two_show">
			<p>编辑表单栏目：</p>
			<div class="table">
				<table>
					<tr>
						<th>名称</th>
						<th>操作</th>
						<th>排序</th>
					</tr>
					<tr v-for="(table,index) in itemMdata.siteFormTable">
						<td>{{table.name}}</td>
						<td>
							<span class="yes" @click="changeyes($event,table)"></span>
							<span class="no" @click="changeno($event,table)"></span>
						</td>
						<td>
							<div class="sort">
								<span class="up" v-show="index != 0" @click="changeup(index)"></span>
								<span class="down" @click="changedown(index)"></span>
							</div>
						</td>
					</tr>
				</table>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	export default {
		name: 'siteFormEdit',
		props: ['itemMdata'],
		data() {
			return {
				title: '',
				dialogImageUrl: '',
				dialogVisible: false,
				one_show: true,
				two_show: false,
				color1: '#409EFF',
				width_hid: true,
				way_hid: true
			}
		},
		created() {
			console.log(this.itemMdata);
		},
		methods: {
			oneshow: function() {
				this.one_show = true;
				this.two_show = false;
			},
			twoshow: function() {
				this.one_show = false;
				this.two_show = true;
			},
			changeyes: function(e,table) {
				table.show = false;
				var table2 = document.querySelector('.table');
				var list = table2.querySelectorAll('tr');
				for(var i = 0; i < list.length; i++) {
					list[i].onclick = function() {
						$(this).find(".yes").css({
							"display": "none"
						});
						$(this).find(".no").css({
							"display": "block"
						});
					}
				}
			},
			changeno: function(e,table) {
				table.show = true;
				var table2 = document.querySelector('.table');
				var list = table2.querySelectorAll('tr');
				for(var i = 0; i < list.length; i++) {
					list[i].onclick = function() {
						$(this).find(".yes").css({
							"display": "block"
						});
						$(this).find(".no").css({
							"display": "none"
						});
					}
				}
			},
			changeup: function(index) {
				this.itemMdata.siteFormTable.splice(index - 1, 0, (this.itemMdata.siteFormTable[index]));
				this.itemMdata.siteFormTable.splice(index + 1, 1);
			},
			changedown: function(index) {
				this.itemMdata.siteFormTable.splice(index + 2, 0, (this.itemMdata.siteFormTable[index]));
				this.itemMdata.siteFormTable.splice(index, 1);
				if(index == this.itemMdata.siteFormTable.length - 1) {
					alert("已经是最后一项啦！");
				}
			},
			arrangeone: function() {
				this.itemMdata.pStyle.float = 'left';
				this.itemMdata.pStyle.width = '48%';
				this.itemMdata.pStyle.margin = '10px 5px';
				this.itemMdata.pStyle.clear = 'none';
				this.itemMdata.inputStyle.width = '75%';
			},
			arrangetwo: function() {
				this.itemMdata.pStyle.float = 'none';
				this.itemMdata.pStyle.width = '100%';
				this.itemMdata.pStyle.margin = '10px 5px';
				this.itemMdata.pStyle.clear = 'both';
				this.itemMdata.inputStyle.width = '88%';
			},
			showone: function() {
				this.itemMdata.spanStyle.float = 'none';
				this.itemMdata.spanStyle.width = '90px';
				this.itemMdata.spanStyle.display = 'block';
				this.itemMdata.spanStyle.textAlign = 'left';
				this.itemMdata.inputStyle.float = 'none';
				this.itemMdata.inputStyle.width = '90%';
			},
			showtwo: function() {
				this.itemMdata.spanStyle.float = 'left';
				this.itemMdata.spanStyle.width = '90px';
				this.itemMdata.spanStyle.textAlign = 'right';
				this.itemMdata.inputStyle.float = 'left';
				this.itemMdata.inputStyle.width = '75%';
			}
		},
		computed: {
			...mapGetters(["state"])
		}
	}
</script>

<style lang="scss" scoped>
	.siteFormEdit {
		box-sizing: border-box;
		height: 450px;
		overflow: auto;
		.top {
			clear: both;
			overflow: hidden;
			padding-left: 10px;
			background: #F5F5F5;
			border-bottom: 1px solid #CCCCCC;
			p {
				float: left;
				padding: 0 15px;
				line-height: 35px;
				border: 1px solid #E3E3E3;
			}
			.active {
				background: #FFFFFF;
				color: #409EFF;
			}
		}
		.one {
			box-sizing: border-box;
			padding: 20px;
			.name {
				clear: both;
				overflow: hidden;
				span {
					float: left;
					width: 120px;
					text-align: right;
				}
				input {
					border: 1px solid #E3E3E3;
					padding-left: 10px;
					line-height: 30px;
				}
			}
			.show {
				clear: both;
				overflow: hidden;
				margin-top: 5px;
				span {
					width: 120px;
					text-align: right;
					float: left;
				}
				ul {
					width: 80%;
					float: left;
					li {
						float: left;
						width: 102px;
						height: 82px;
						border: 1px solid #E3E3E3;
						text-align: center;
						margin-left: 5px;
						margin-right: 15px;
						margin-top: 10px;
						div {
							width: 100%;
							height: 82px;
							background: url(../../../static/image/mbg02.png)no-repeat;
						}
						.show_one {
							background-position: 0 0;
						}
						.show_two {
							background-position: 0 -99px;
						}
					}
					li:hover {
						border: 1px solid #3a8ee6;
					}
				}
			}
			.arrange {
				clear: both;
				overflow: hidden;
				margin-top: 10px;
				span {
					width: 120px;
					text-align: right;
					float: left;
				}
				ul {
					width: 80%;
					float: left;
					li {
						float: left;
						width: 102px;
						height: 82px;
						border: 1px solid #E3E3E3;
						text-align: center;
						margin-left: 5px;
						margin-right: 15px;
						margin-top: 10px;
						div {
							width: 100%;
							height: 82px;
							background: url(../../../static/image/mbg02.png)no-repeat;
						}
						.arrange_one {
							background-position: 0 -196px;
						}
						.arrange_two {
							background-position: 0 -297px;
						}
					}
					li:hover {
						border: 1px solid #3a8ee6;
					}
				}
			}
			.way {
				clear: both;
				overflow: hidden;
				margin-top: 10px;
				span {
					width: 120px;
					text-align: right;
					float: left;
				}
				p {
					float: left;
					margin-left: 10px;
				}
				.way_hid {
					clear: both;
					overflow: hidden;
					input {
						border: 1px solid #E3E3E3;
						width: 160px;
						line-height: 30px;
						padding-left: 10px;
					}
					button {
						border: 1px solid #E3E3E3;
						text-align: center;
						padding: 0px 10px;
						line-height: 30px;
						margin-left: 10px;
					}
				}
			}
		}
		.two {
			box-sizing: border-box;
			padding: 20px;
			.table {
				height: 300px;
				overflow: auto;
				border: 1px solid #e7ecf0;
				table {
					width: 100%;
					color: #666666;
					th {
						background: #f8f9fb;
						line-height: 45px;
					}
					td {
						text-align: center;
						line-height: 45px;
						.yes {
							display: block;
							width: 16px;
							height: 16px;
							background: url(../../../static/image/bg01.png)no-repeat;
							background-position: -1405px -322px;
							margin-left: 45%;
						}
						.no {
							display: none;
							width: 16px;
							height: 16px;
							background: url(../../../static/image/bg01.png)no-repeat;
							background-position: -1441px -322px;
							margin-left: 45%;
						}
						.sort {
							width: 30px;
							height: 15px;
							margin-left: 41%;
							.up {
								float: left;
								display: block;
								width: 8px;
								height: 13px;
								background: url(../../../static/image/component.png) no-repeat;
								background-position: -635px 0;
							}
							.down {
								float: left;
								display: block;
								margin-left: 8px;
								width: 8px;
								height: 13px;
								background: url(../../../static/image/component.png) no-repeat;
								background-position: -651px 0;
							}
						}
					}
					tr:nth-child(2n-1) {
						background: #f8f9fb;
					}
				}
			}
		}
	}
	
	::-webkit-scrollbar {
		width: 6px;
		height: 6px;
		background-color: #f1f1f1;
	}
	
	::-webkit-scrollbar-thumb {
		background-color: #b2b2b2;
		width: 16px;
		-webkit-border-radius: 16px;
		-moz-border-radius: 16px;
		border-radius: 16px;
	}
</style>


