<template>
	<el-dialog class="callout_link" :title="editDial.artTitle" :visible.sync="editDial.setlink" width="40%">
		<div class="link_con">
			<el-form ref="form" :model="form" label-width="120px">
				<el-form-item label="链接类型：" label-width="120px">
					<el-radio-group v-model="form.newLinkTypeSwitch">
						<el-radio label="1">站内链接</el-radio>
						<el-radio label="2">功能</el-radio>
						<el-radio label="3">站外链接</el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<el-form v-show="form.newLinkTypeSwitch==1" ref="form0" :model="form" label-width="120px">
				<el-form-item label="站内链接：">
					<el-select v-model="form0.colsType" @change="changColsType" placeholder="请选择链接">
						<el-option v-for="rut in router.navItem" :key="rut.link" :label="rut.tText" :value="rut.link">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="网站栏目：">
					<el-select v-model="form0.colTitle" @change="changcolTitle" placeholder="请选择栏目">
						<el-option v-for="(sec,index) in secondNav" :key="index" :label="sec.tText" :value="sec.link"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="打开方式：">
					<el-checkbox-group v-model="form0.openByNewPage">
						<el-checkbox label="在新页面打开" name="type"></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
			</el-form>
			<el-form v-show="form.newLinkTypeSwitch==2" ref="form" :model="form1" label-width="120px">
				<el-form-item label="选择功能：">
					<el-select v-model="form1.newMenuWord" placeholder="请选择链接">
						<el-option label="区域一" value="shanghai"></el-option>
						<el-option label="区域二" value="beijing"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="选择弹窗：">
					<el-select v-model="form1.popupZoneBox" placeholder="请选择栏目">
						<el-option label="区域一" value="shanghai"></el-option>
						<el-option label="区域二" value="beijing"></el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<el-form v-show="form.newLinkTypeSwitch==3" ref="form" :model="form2" label-width="120px">
				<el-form-item label="自定义地址：">
					<el-input v-model="form2.cusLink" style="width: 220px;"></el-input>
				</el-form-item>
				<el-form-item label="打开方式：">
					<el-checkbox-group v-model="form2.openByNewPage">
						<el-checkbox label="在新页面打开" name="type"></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
			</el-form>
		</div>
		<span slot="footer" class="dialog-footer">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="mSave">确 定</el-button>
  </span>
	</el-dialog>
</template>

<script>
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	export default {
		name: 'uiLink',
		created() {
			//console.log("uiLink");
			//console.log(this.itemMdata);
		},
		components: {

		},
		props: ['itemMdata', 'editDial'],
		mounted: function() {

		},
		methods: {
			changColsType() {
				var navItem = this.router.navItem;
				for(var i = 0; navItem.length > i; i++) {
					if(navItem[i].link == this.form0.colsType) {
						console.log(navItem[i].secondNav);
					    this.secondNav = navItem[i].secondNav || [];
					}
				}
				//debugger;
				//this.form0.colTitle = "";
				//this.itemMdata.link = this.form0.colsType;
				///debugger;
			},
			cancel() {
				this.editDial.setlink = false;
			},
			mSave() {
				this.saveloca(this.id);
				this.editDial.setlink = false;
			},
			changcolTitle() {
				if(this.form0.colTitle) {
					this.itemMdata.link = this.form0.colTitle;
				}
			},
			...mapActions([
				'saveloca'
			])

		},
		computed: {
			...mapGetters(["router", "state"])
		},
		data() {
			return {
				title: "设置标题",
				id: this.$route.params.id,
				form: {
					newLinkTypeSwitch: "1"
				},
				secondNav: [],
				form0: {
					colsType: '',
					colTitle: '',
					openByNewPage: ''
				},
				form1: {
					popupZoneBox: '',
					newMenuWord: ''
				},
				form2: {
					cusLink: "",
					openByNewPage: ""
				}
			}
		}
	}
</script>

<style lang="scss" scoped>

</style>


