<template>
		<div class="hove_con font" @dblclick="linkUrl">
			
			<div v-bind:id="editorId" contenteditable="true" cols="20" rows="2" class="ckeditor">
				<p class="cancel" v-html="itemMdata.tCon">点击文字实现可编辑状态...</p>
			</div>
		</div>
</template>

<script>
	export default {
		name: 'mFont',
		
		props: ['itemMdata', "posCom", 'index'],
		mounted() {
			/* CKEDITOR.replace(this.editorId, {height: "300px", width: "100%", toolbar: "Full"});
      var editor = CKEDITOR.instances.editor2;*/
			var editor = CKEDITOR.inline(this.editorId, {
				height: '30'
				//extraAllowedContent: 'a(documentation);abbr[title];code',
				//removePlugins: 'stylescombo',
				//extraPlugins: 'sourcedialog',
				//startupFocus: false
			});
			var t = this;
			editor.on('change', function(event) {
				t.itemMdata.tCon = this.getData(); //内容			   
			});
			
		},
		methods: {
			getDataEditor() {
				const data = this.editor.getData();
			},
			linkUrl() {
				if(this.itemMdata.link) {
					if(this.itemMdata.link.indexOf("http") != -1) {
						location.href = this.itemMdata.link;
					} else {
						this.$router.push(this.preview + this.itemMdata.link);
						//location.href = this.preview + this.itemMdata.link;
					}
				}
				console.log(this.preview + this.itemMdata.link);
			},
			onResize: function(x, y, width, height) {
				this.parentStyle.left = x;
				this.parentStyle.top = y;
				this.parentStyle.width = width;
				this.parentStyle.height = height;
				this.itemMdata.parentStyle = this.parentStyle;
			},
			removeComponent(i) {
				this.$emit('removeComponent', i);
			},
			onDrag: function(x, y) {
				this.itemMdata.parentStyle.left = x;
				this.itemMdata.parentStyle.top = y;
			}
		},
		created() {
			console.log(this.itemMdata);
		},
		data() {
			return {
				title: '文本',
				preview: '/t1/' + this.$route.params.id + "/",
				id: this.$route.params.id,
				parentStyle: {

				},
				editDial: {
					artTitle: "设置链接地址",
					setlink: false
				},
				editorId: "editor" + this.posCom + this.index,
				editor: {}
			}
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.hove_con {
		position: relative;
		.edit_con {
			position: absolute;
			top: -40px;
			width: 500px;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				cursor: pointer;
			}
		}
	}
	
	.hove_con:hover {
		.edit_con {
			display: block;
		}
	}
</style>


