Vue.directive('dragresize', //自定义指令 JS
	{
		bind: function(el, binding) {
			console.log(el);
			var theobject = null; //

			function resizeObject() {
				this.el = null; //
				this.dir = ""; //(n, s, e, w, ne, nw, se, sw)
				this.grabx = null; //
				this.graby = null;
				this.width = null;
				this.height = null;
				this.left = null;
				this.top = null;
			}

			//
			function getDirection(el) {
				var xPos, yPos, offset, dir;
				dir = "";

				xPos = window.event.offsetX;
				yPos = window.event.offsetY;

				offset = 20; //The distance from the edge in pixels

				if(yPos < offset) dir += "n";
				else if(yPos > el.offsetHeight - offset) dir += "s";
				if(xPos < offset) dir += "w";
				else if(xPos > el.offsetWidth - offset) dir += "e";

				return dir;
			}
			var dir = "";

			function doDown() {
				//var el = getReal(event.srcElement, "className", "resizeMe");
				if(el == null) {
					theobject = null;
					return;
				}

				dir = getDirection(el);
				if(dir == "") return;

				theobject = new resizeObject();
				theobject.el = el;
				theobject.dir = dir;

				theobject.grabx = window.event.clientX;
				theobject.graby = window.event.clientY;
				theobject.width = el.offsetWidth;
				theobject.height = el.offsetHeight;
				theobject.left = el.offsetLeft;
				theobject.top = el.offsetTop;

				document.onmousemove = doMove;
				//event.preventDefault();
				//window.event.returnValue = false;
				//window.event.cancelBubble = true;
			}

			function doUp() {
				if(theobject != null) {
					theobject = null;
				}
				document.onmousemove = null;
				el.style.cursor = null;
			}

			function elDoUp() {
				if(theobject != null) {
					theobject = null;
				}
				document.onmousemove = null;
				var xPos, yPos, str, xMin, yMin;
				xMin = 8; //The smallest width possible
				yMin = 8; //height
				if(el.className.indexOf("resizeMe") != -1) {
					str = getDirection(el);
					//Fix the cursor 
					if(str == "") str = "default";
					else str += "-resize";
					el.style.cursor = str;
				}
			}

			function doMove() {
				var xPos, yPos, str, xMin, yMin;
				xMin = 8; //The smallest width possible
				yMin = 8; //height

				//el = getReal(event.srcElement, "className", "resizeMe");

				if(el.className.indexOf("resizeMe") != -1) {
					str = getDirection(el);
					//Fix the cursor 
					if(str == "") str = "default";
					else str += "-resize";
					el.style.cursor = str;
					if(theobject != null) {
						console.log(dir);
						if(dir.indexOf("e") != -1) {
							if(theobject.width + window.event.clientX - theobject.grabx >= 990) {
								theobject.el.style.width = '990px'
								return
							}
							//theobject.el.style.width = Math.max(xMin, theobject.width + window.event.clientX - theobject.grabx) + "px";
						}
						if(dir.indexOf("s") != -1)
							theobject.el.style.height = Math.max(yMin, theobject.height + window.event.clientY - theobject.graby) + "px";

						if(dir.indexOf("w") != -1) {
							if(theobject.width - window.event.clientX + theobject.grabx >= 990) {
								theobject.el.style.width = '990px'
								return
							}
							theobject.el.style.left = Math.min(theobject.left + window.event.clientX - theobject.grabx, theobject.left + theobject.width - xMin) + "px";
							//theobject.el.style.width = Math.max(xMin, theobject.width - window.event.clientX + theobject.grabx) + "px";

						}
						if(dir.indexOf("n") != -1) {
							theobject.el.style.top = Math.min(theobject.top + window.event.clientY - theobject.graby, theobject.top + theobject.height - yMin) + "px";
							theobject.el.style.height = Math.max(yMin, theobject.height - window.event.clientY + theobject.graby) + "px";
						}
						$(".multi_con").each(function(v) {
							var h = $(this).height();
							$(this).find(".area_conn").height(h);
							$(this).find(".clm").height(h);
						})
						//console.log(binding)
						binding.value({
							height: theobject.el.style.height,
							el: theobject.el.id
						});
						//window.event.returnValue = false;
						//window.event.cancelBubble = true;
					}

				}
				console.log('el');
				//Dragging starts here
			}

			function getReal(el, type, value) {
				var temp = el;
				//debugger
				while((temp != null) && (temp.tagName != "BODY")) {
					if(eval("temp." + type) == value) {
						el = temp;
						return el;
					}
					temp = temp.parentElement;
				}
				return el;
			}

			el.onmousedown = doDown;
			document.onmouseup = doUp;
			el.onmouseup = elDoUp;
			//document.onmousemove = doMove;
			console.log(el.onmouseup)
		}
	}
);

Vue.directive('to-focus', //自定义指令 JS
	{
		bind: function(el, binding) {
			if(binding.value) {
				el.focus();
				console.log(el)
			}
		}
	}
);
