<template>
	<div class="fontEdit">
		<!--ul class="it_com">
			<li class="cur">文本组件</li>
			<li>组件样式</li>
		</ul-->
		<div class="item cm-emstr">
			<div class="con">
				<div class="f_n">
					<div class="ui-widget">
						<mSelect :mStyle='fontEdit.style' :flag="'fontSize'" v-on:changeFontSize="changeFontSize"></mSelect>
					</div>
					<span class="bold">
					      <span v-bind:class="{'icon_b':true,cur:boldCur}" @click="bold">B</span>
					</span>
					<span class="italic">
					      <span v-bind:class="{'icon_b':true,cur:italicCur}" @click="italic" class="icon_italic">I</span>
					</span>
					<span class="underline">
					      <span v-bind:class="{'icon_b':true,cur:underlineCur}"  @click="underline" class="icon_underline">U</span>
					</span>
					<el-color-picker v-model="fontEdit.style.color" size="small" color-format="hex"></el-color-picker>
				</div>			
			</div>
		</div>
	
	</div>
</template>

<script>
	import mSelect from './select.vue'
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	export default {
		name: 'fontEdit',
		components: {
			mSelect
		},
		data() {
			return {
				color3: '#eee',
				title: "编辑文本",
				boldCur: false,
				italicCur: true,
				underlineCur: false,
				//style:this.fontEdit.style,
				dat: {
					content: ''
				}
			}
		},
		props: ['fontEdit'],
		created() {
			console.log('created fontEdit')
			console.log(this.fontEdit)
			this.fontEdit.style.width = "120px";
		},
		props: ['fontEdit'],
		methods: {
			bold() {
				this.boldCur = !this.boldCur;
				this.fontEdit.style.fontWeight = this.boldCur ? 'bold' : 'normal';
				console.log("bold")
			},
			italic() {
				this.italicCur = !this.italicCur;
				this.fontEdit.style.fontStyle = this.italicCur ? 'italic' : 'normal';
				console.log("italic")
				console.log(this.state)
			},
			underline() {
				this.underlineCur = !this.underlineCur;
				this.fontEdit.style.textDecoration = this.underlineCur ? 'underline' : 'none';
				console.log("underline");
				console.log(this.state)
			},
			changeWidthSize: function(size) {
				this.fontEdit.style.width = size;
			},
			changeLineHeightSize: function(size) {
				this.fontEdit.style.lineHeight = size;
			},
			changeFontSize(size, flag) {
				//console.log(flag)
				//console.log(size)
				if(flag == 'fontSize') {
					this.fontEdit.style.fontSize = size;
				} else if(flag == 'width') {
					console.log("width")
					this.fontEdit.style.width = size;
				} else if(flag == 'lineHeight') {
					console.log("lineHeight")
					this.fontEdit.style.lineHeight = size;
				}
				//flag=='lineHeight'&&this.fontEdit.style.lineHeight = size;
				//flag=='width'&&this.fontEdit.style.width = size;
				//flag=='fontSize'&&this.fontEdit.style.fontSize = size;
				console.log(this.fontEdit)
			},
			...mapMutations([
				'modifyItem'
			])
		},

		computed: {
			...mapGetters(["state"])
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
	.f_n {
		height: 40px;
	}	
	.fontEdit {
		background-color: #fff;
		.it_com {
			height: 40px;
			line-height: 40px;
			border-bottom: 1px solid #D6D6D6;
			li {
				float: left;
				cursor: pointer;
				width: 30%;
				text-align: center;
			}
			.cur {
				color: #efbf1f;
				background: #F6F6F6;
				border-bottom: 2px solid #efbf1f;
				height: 39px;
			}
		}
	}
	
	.icon_bold {
		font-weight: 700;
	}
	
	.bold {
		padding: 3px;
		font-weight: 700;
		float: left;
	}
	
	.italic,
	.underline {
		padding: 3px;
		margin: 0 10px;
		font-weight: 700;
		float: left;
	}
	
	.cm-emstr .con .icon_b {
		padding: 3px 6px;
		cursor: pointer;
	}
	
	.cm-emstr .con .cur {
		background-color: #2cb9fe;
	}
	
	.ristine {
		textarea {
			border: 1px solid #d3d3d3;
			width: 300px;
			height: 140px;
		}
	}
	
	.ui-widget {
		width: 50px;
		float: left;
	}
	
	.ui-butn {
		float: left;
		width: 300px;
		input {
			float: left;
			padding: 3px 5px;
		}
	}
	
	.ite_0 {
		h2 {
			background: #e5e5e5;
			padding-left: 15px;
			line-height: 28px;
		}
		.con_lot {
			float: left;
			.item {
				float: left;
				.s_l {
					float: left;
				}
				.s {
					float: left;
					width: 55px;
				}
			}
		}
		label {
			float: left;
			width: 55px;
		}
	}
	
	.ite_1 {
		h2 {
			background: #e5e5e5;
			padding-left: 15px;
			line-height: 28px;
		}
	}
</style>


