<template>
	<vue-draggable-resizable :x="itemMdata.parentStyle.left" :y="itemMdata.parentStyle.top" :w="itemMdata.parentStyle.width" :h="itemMdata.parentStyle.height" v-on:dragging="onDrag()" :parent="itemMdata.parent" v-on:resizing="onResize()" :drag-cancel="'.edit_con'">
		<div class="siteForm hove_con">
			<p class="edit_con">
				<span @click="itemMdata.isEdit = true">编辑在线表单</span>
				<span class="icon style"><i>设置样式</i></span>
				<span class="icon animation"><i>设置动画</i></span>
				<span class="icon borderhid"><i>隐藏边框</i></span>
				<span @click="removeComponent(itemMdata)" class="icon delete"><i>删除模块</i></span>
			</p>
			<div class="detail">
				<p v-for="list in itemMdata.siteFormTable" :style="itemMdata.pStyle" v-show="list.show">
					<span :style="itemMdata.spanStyle">{{list.name}}：</span>
					<input type="text" :placeholder="'请输入您的'+list.name" :style="itemMdata.inputStyle" />
				</p>
				<!--<p :style="itemMdata.pStyle">
					<span :style="itemMdata.spanStyle">姓名：</span>
					<input type="text" placeholder="请输入您的姓名" :style="itemMdata.inputStyle" />
				</p>
				<p :style="itemMdata.pStyle">
					<span :style="itemMdata.spanStyle">性别：</span>
					<input type="text" placeholder="请输入您的性别" :style="itemMdata.inputStyle" />
				</p>
				<p :style="itemMdata.pStyle">
					<span :style="itemMdata.spanStyle">联系电话：</span>
					<input type="text" placeholder="请输入您的联系电话" :style="itemMdata.inputStyle" />
				</p>
				<p :style="itemMdata.pStyle">
					<span :style="itemMdata.spanStyle">联系地址：</span>
					<input type="text" placeholder="请输入您的联系地址" :style="itemMdata.inputStyle" />
				</p>-->
				<textarea rows="10" cols="100%" type="text" placeholder="留言内容：" :style="itemMdata.textareaStyle"></textarea>
			</div>
		</div>
		<uiDialog v-if="itemMdata.isEdit" :itemMdata='itemMdata' :editDial="editDial"></uiDialog>
	</vue-draggable-resizable>
</template>

<script>
	import VueDraggableResizable from 'vue-draggable-resizable'
	import uiDialog from './uiDialog.vue'
	export default {
		name: 'siteForm',
		components: {
			VueDraggableResizable,
			uiDialog
		},
		props: ['itemMdata'],
		created() {
			console.log(this.itemMdata)
		},
		methods: {
			onResize: function(x, y, width, height) {
				this.parentStyle.left = x;
				this.parentStyle.top = y;
				this.parentStyle.width = width;
				this.parentStyle.height = height;
				this.itemMdata.parentStyle = this.parentStyle;
			},
			onDrag: function(x, y) {
				this.itemMdata.parentStyle.left = x;
				this.itemMdata.parentStyle.top = y;
			},
			removeComponent(i) {
				this.$emit('removeComponent', i);
			}
		},
		data() {
			return {
				outerVisible: false,
				parentStyle: {

				},
				editDial: {
					editDialWidth: "650px",
					editDialHeight: "580px",
					rTitle: '编辑在线表单',
				}
			}
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.hove_con {
		position: relative;
		height: 100%;
		.edit_con {
			position: absolute;
			background: #FFFFFF;
			border-radius: 5px;
			box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.2);
			padding: 0 15px;
			top: -40px;
			width: auto;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				float: left;
				cursor: pointer;
				border-right: 1px solid #E3E3E3;
				margin: 0 8px;
			}
			span:nth-child(1) {
				padding-right: 7px;
				line-height: 25px;
				margin-top: 8px;
			}
			.icon {
				display: block;
				float: left;
				border-right: none;
				background: url(../../../static/image/bg03.png)no-repeat;
			}
			.icon i {
				position: absolute;
				display: block;
				font-size: 12px;
				width: auto;
				line-height: 25px;
				padding: 0px 10px;
				background: #FFFFFF;
				border-radius: 5px;
				box-shadow: 0 1px 4px 2px rgba(0, 0, 0, 0.1);
				top: -23px;
				margin-left: -25px;
				display: none;
			}
			.style {
				background-position: -60px -186px;
				width: 21px;
				height: 21px;
				margin-top: 10px;
			}
			.style:hover {
				background-position: -60px -232px;
			}
			.style:hover i {
				display: block;
			}
			.animation {
				background-position: -372px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.animation:hover {
				background-position: -372px -232px;
			}
			.animation:hover i {
				display: block;
			}
			.borderhid {
				background-position: -275px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.borderhid:hover {
				background-position: -275px -232px;
			}
			.borderhid:hover i {
				display: block;
			}
			.delete {
				background-position: -105px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.delete:hover {
				background-position: -105px -232px;
			}
			.delete:hover i {
				display: block;
			}
		}
		.detail {
			p {
				overflow: hidden;
				line-height: 40px;
				span {
					text-align: right;
				}
				input {
					line-height: 40px;
					border: 1px solid #E3E3E3;
					border-radius: 5px;
					padding-left: 10px;
				}
			}
			textarea {
				border: 1px solid #E3E3E3;
				border-radius: 5px;
				margin-top: 15px;
				padding: 10px;
				box-sizing: border-box;
			}
		}
	}
	
	.hove_con:hover {
		.edit_con {
			display: block;
		}
	}
</style>


