<template>
	<vue-draggable-resizable :x="itemMdata.parentStyle.left" :y="itemMdata.parentStyle.top" :w="itemMdata.parentStyle.width" :h="itemMdata.parentStyle.height" v-on:dragging="onDrag()" :parent="itemMdata.parent" v-on:resizing="onResize()" :drag-cancel="'.edit_con'">
		<div class="productShow hove_con">
			<p class="edit_con">
				<span @click="itemMdata.isEdit = true">编辑产品展示</span>
				<span class="icon style"><i>设置样式</i></span>
				<span class="icon animation"><i>设置动画</i></span>
				<span class="icon borderhid"><i>隐藏边框</i></span>
				<span @click="removeComponent(itemMdata)" class="icon delete"><i>删除模块</i></span>
			</p>
			<div class="detail" :id="productShowId">
				<!--one-->
				<ul v-show="itemMdata.styleone">
					<li v-for="(list,index) in itemMdata.productSelected" :style="itemMdata.liStyle" :class="{mouseenterOne:itemMdata.mouseenterNumber==1,mouseenterTwo:itemMdata.mouseenterNumber==2,mouseenterThree:itemMdata.mouseenterNumber==3,mouseenterFour:itemMdata.mouseenterNumber==4}">
						<div @dblclick="gotolink(list.url,list.target)">
							<img :src="list.img" :style="itemMdata.imgStyle" />
							<p :style="itemMdata.pStyle">{{list.name}}</p>
							<div class="hid" :style="itemMdata.hidStyle">
								<p>{{list.describe}}</p>
								<i></i>
							</div>
						</div>
					</li>
				</ul>
				<!--<ul>
					<li v-for="(table,index) in itemMdata.productShowTable">
						<p v-show="table.show">{{table.name}}</p>
					</li>
				</ul>-->
				<!--two-->
				<div class="carousel-wrap slide1" v-show="itemMdata.styletwo">
					<div class="width">
						<ul class='slide-ul'>
							<li v-for="(list,index) in itemMdata.productSelected" :style="itemMdata.liStyle" :class="{mouseenterOne:itemMdata.mouseenterNumber==1,mouseenterTwo:itemMdata.mouseenterNumber==2,mouseenterThree:itemMdata.mouseenterNumber==3,mouseenterFour:itemMdata.mouseenterNumber==4}">
								<div @dblclick="gotolink(list.url,list.target)">
									<img :src="list.img" :style="itemMdata.imgStyle" />
									<p :style="itemMdata.pStyle">{{list.name}}</p>
									<div class="hid" :style="itemMdata.hidStyle">
										<p>{{list.describe}}</p>
										<i></i>
									</div>
								</div>
							</li>
						</ul>
					</div>
					<div class="button">
						<span class="prev" id="prev2"><</span>
						<span class="next" id="next2">></span>
					</div>
				</div>
				<!--three-->
				<div class="carousel-wrap slide2" v-show="itemMdata.stylethree">
					<ul class='slide-ul'>
						<li v-for="(list,index) in itemMdata.productSelected" :style="itemMdata.liStyle" :class="{mouseenterOne:itemMdata.mouseenterNumber==1,mouseenterTwo:itemMdata.mouseenterNumber==2,mouseenterThree:itemMdata.mouseenterNumber==3,mouseenterFour:itemMdata.mouseenterNumber==4}">
							<div @dblclick="gotolink(list.url,list.target)">
								<img :src="list.img" :style="itemMdata.imgStyle" />
								<p :style="itemMdata.pStyle">{{list.name}}</p>
								<div class="hid" :style="itemMdata.hidStyle">
									<p>{{list.describe}}</p>
									<i></i>
								</div>
							</div>
						</li>
					</ul>
				</div>
				<!--分页-->
				<div class="page" v-show="itemMdata.page">
					<p>
						<a href="">首页</a>
					</p>
					<p>
						<a href="">1</a>
					</p>
					<p>
						<a href="">2</a>
					</p>
					<p>
						<a href="">3</a>
					</p>
					<p>
						<a href="">4</a>
					</p>
					<p>
						<a href="">末页</a>
					</p>
				</div>
			</div>
		</div>
		<uiDialog v-if="itemMdata.isEdit" :itemMdata='itemMdata' :editDial="editDial"></uiDialog>
	</vue-draggable-resizable>
</template>

<script>
	import VueDraggableResizable from 'vue-draggable-resizable'
	require("../../../static/js/emit.js");
	import uiDialog from './uiDialog.vue'
	export default {
		name: 'productShow',
		components: {
			VueDraggableResizable,
			uiDialog
		},
		props: ['itemMdata', "posCom", 'index'],
		data() {
			return {
				outerVisible: false,
				parentStyle: {

				},
				editDial: {
					editDialWidth: "650px",
					editDialHeight: "580px",
					rTitle: '编辑产品展示',
				},
				marginleft: '',
				marginleft2: '',
				currentIndex: 0,
				currentIndex2: 0,
				productShowId: "productShow" + this.posCom + this.index
			}
		},
		created() {
			console.log(this.itemMdata);
			//			this.$nextTick(() => {
			//				setInterval(() => {
			//					this.marginleft2 -= 300;
			//					this.currentIndex2++;
			//					if(this.currentIndex2 > this.itemMdata.productSelected.length - 1) {
			//						this.marginleft2 = 0;
			//						this.currentIndex2 = 0;
			//					}
			//				}, 2000)
			//			})
		},
		mounted() {
			productShow.productShow1(this.productShowId);
		},
		methods: {
			gotolink:function(link,target){
				window.open(link,target); 
			},
			removeComponent(i) {
				this.$emit('removeComponent', i);
			},
			onResize: function(x, y, width, height) {
				this.parentStyle.left = x;
				this.parentStyle.top = y;
				this.parentStyle.width = width;
				this.parentStyle.height = height;
				this.itemMdata.parentStyle = this.parentStyle;
			},
			onDrag: function(x, y) {
				this.itemMdata.parentStyle.left = x;
				this.itemMdata.parentStyle.top = y;
			},
			//			prev: function() {
			//				this.marginleft -= 300;
			//				this.currentIndex++;
			//				if(this.currentIndex > this.itemMdata.productSelected.length - 1) {
			//					this.marginleft = 0;
			//					this.currentIndex = 0;
			//				}
			//			},
			//			next: function() {
			//				this.marginleft += 300;
			//				this.currentIndex--;
			//				if(this.currentIndex == -1) {
			//					alert("已经是第一项啦！");
			//					this.marginleft = 0;
			//					this.currentIndex = 0;
			//				}
			//			}
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.hove_con {
		position: relative;
		height: 100%;
		.edit_con {
			position: absolute;
			background: #FFFFFF;
			border-radius: 5px;
			box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.2);
			padding: 0 15px;
			top: -40px;
			width: auto;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				float: left;
				cursor: pointer;
				border-right: 1px solid #E3E3E3;
				margin: 0 8px;
			}
			span:nth-child(1){
				padding-right: 7px;
				line-height: 25px;
				margin-top: 8px;
			}
			.icon{
				display: block;
				float: left;
				border-right: none;
				background: url(../../../static/image/bg03.png)no-repeat;
			}
			.icon i{
				position: absolute;
				display: block;
				font-size: 12px;
				width: auto;
				line-height: 25px;
				padding: 0px 10px;
				background: #FFFFFF;
			    border-radius: 5px;
			    box-shadow: 0 1px 4px 2px rgba(0, 0, 0, 0.1);
			    top: -23px;
			    margin-left: -25px;
			    display: none;
			}
			.style{
				background-position: -60px -186px;
				width: 21px;
				height: 21px;
				margin-top: 10px;
			}
			.style:hover{
				background-position: -60px -232px;
			}
			.style:hover i{
				display: block;
			}
			.animation{
				background-position: -372px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.animation:hover{
				background-position: -372px -232px;
			}
			.animation:hover i{
				display: block;
			}
			.borderhid{
				background-position: -275px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.borderhid:hover{
				background-position: -275px -232px;
			}
			.borderhid:hover i{
				display: block;
			}
			.delete{
				background-position: -105px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.delete:hover{
				background-position: -105px -232px;
			}
			.delete:hover i{
				display: block;
			}
		}
		.detail {
			ul {
				clear: both;
				overflow: hidden;
				li {
					text-align: center;
					overflow: hidden;
					position: relative;
					img {
						height: auto;
						position: relative;
					}
					p {
						line-height: 25px;
						width: 100%;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
					.hid {
						display: none;
						position: absolute;
						left: 0;
						right: 0;
						top: 0;
						bottom: 25px;
						transition: all 0.7s;
						-moz-transition: all 0.7s;
						-webkit-transition: all 0.7s;
						-o-transition: all 0.7s;
					}
				}
				.mouseenterOne:hover .hid {
					display: none;
				}
				.mouseenterTwo:hover .hid {
					display: block;
					background: none !important;
				}
				.mouseenterTwo:hover .hid p {
					display: none;
				}
				.mouseenterThree:hover .hid {
					border: none !important;
					left: 32%;
					right: 32%;
					top: 20%;
					bottom: 60px;
					display: block;
					background: rgba(0, 0, 0, 0.7) !important;
					border-radius: 100%;
				}
				.mouseenterThree:hover .hid p {
					display: none;
				}
				.mouseenterThree:hover .hid i {
					display: block;
					width: 20px;
					height: 20px;
					margin-left: 39%;
					margin-top: 37%;
					background: url(../../../static/image/mbg01.png)no-repeat center;
					background-position: 0 -1085px;
				}
				.mouseenterFour:hover .hid {
					border: none !important;
					display: block;
					background: rgba(0, 0, 0, 0.7);
					text-align: center;
					padding-top: 30%;
				}
				.mouseenterFour:hover .hid p {
					color: #FFFFFF;
				}
			}
			.carousel-wrap {
				width:100%;
				clear: both;
				overflow: hidden;
				position: relative;
				.width {
					width: 90%;
					margin: 0 auto;
					overflow: hidden;
				}
				.slide-ul {
					width: auto;
					overflow: hidden;
					position: relative;
				}
				.button {
					span {
						font-size: 24px;
						font-family: "agency fb";
						color: #FFFFFF;
						background: #409EFF;
						padding: 0px 15px;
						border-radius: 100%;
						position: absolute;
						cursor: pointer;
					}
					.prev {
						left: 0px;
						top: 70px;
					}
					.next {
						left: auto;
						right: 0px;
						top: 70px;
					}
				}
			}
			.page {
				margin-top: 10px;
				display: table;
				text-align: center;
				margin-left: auto;
				margin-right: auto;
			}
			.page p {
				float: left;
				margin-right: 10px;
			}
			.page p a {
				display: block;
				border-radius: 3px;
				color: #666;
				border: 1px solid #b3b3b3;
				text-decoration: none;
				padding: 2px 10px;
				font-size: 14px;
			}
			.page p a:hover {
				background: #5874d8;
				color: #FFFFFF;
			}
		}
	}
	
	.hove_con:hover {
		.edit_con {
			display: block;
		}
	}
</style>


