<template>
	<vue-draggable-resizable class="multiCarousel" :x="itemMdata.parentStyle.left" :y="itemMdata.parentStyle.top" :w="itemMdata.parentStyle.width" :h="itemMdata.parentStyle.height||200" :parent="itemMdata.parent" v-on:resizing="onResize" v-on:dragging="onDrag" :drag-cancel="'.edit_con'">
		<div class="multiCarousel hove_con">
			<p class="edit_con">
				<span @click="itemMdata.isEdit = true">编辑轮播多图</span>
				<span class="icon style"><i>设置样式</i></span>
				<span class="icon animation"><i>设置动画</i></span>
				<span class="icon borderhid"><i>隐藏边框</i></span>
				<span @click="removeComponent(itemMdata)" class="icon delete"><i>删除模块</i></span>
			</p>
			<div class="carousel-wrap" :id="carouselId" :style="style">
				<ul class="slide-ul activeimg" :style="style">
					<li v-for="(list,index) in itemMdata.slideList" :key="index" :style="style">
						<img @mousedown="defaultStop($event)" :src="list.url" :alt="list.name">
					</li>
				</ul>
				<!--div class="bot-img">
					<ul>
						<li v-for="(list,index) in slideList" :key="index"><img @mousedown="defaultStop($event)" :src="list.image" :alt="list.name"></li>
						<!--li><img src="/static/image/img/2.jpg"></li>
						<li><img src="/static/image/img/3.jpg"></li>
						<li><img src="/static/image/img/4.jpg"></li>
						<li><img src="/static/image/img/5.jpg"></li-->
				<!---/ul>
				</div-->
				<div class="carousel-is" :class="[{'carousel-items':100==itemMdata.curIndex,'carousel-is0':0==itemMdata.curIndex,'carousel-is1':1==itemMdata.curIndex,'carousel-is2':2==itemMdata.curIndex,'carousel-is3':3==itemMdata.curIndex,'carousel-is4':4==itemMdata.curIndex}]">
					<div class="emron"><em class="r"></em><em class="l"></em></div>
					<span :data-index="index" v-for="(item,index) in itemMdata.slideList" :class="{'active':index===currentIndex}">{{index+1}}</span>
				</div>
			</div>
		</div>
		<uiDialog v-if="itemMdata.isEdit" :itemMdata='itemMdata' :editDial="editDial"></uiDialog>
	</vue-draggable-resizable>
</template>

<script>
	import VueDraggableResizable from 'vue-draggable-resizable'
	require("../../../static/js/emit.js");
	import uiDialog from './uiDialog.vue'
	export default {
		name: 'multiCarousel',
		components: {
			VueDraggableResizable,
			uiDialog
		},
		props: ['itemMdata', "posCom", 'index'],
		created() {
			//this.itemMdata.parentStyle.width = this.carouse;
			console.log(this.itemMdata);
			//debugger;
		},
		data() {
			return {
				title: "轮播多图",
				outerVisible: false,
				carouselId: "carousel" + this.posCom + this.index,
				width: 0,
				carouse: 300,
				height: 0,
				style: {
					width: this.itemMdata.parentStyle.width + 'px',
					height: this.itemMdata.parentStyle.height + 'px'
				},
				x: 0,
				y: 0,
				parentStyle: {

				},
				currentIndex: 0,
				timer: '',
				editDial: {
					editDialWidth: "950px",
					editDialHeight: "680px",
					rTitle: '编辑轮播图'
				}
			}
		},
		mounted() {
			multiCarouse.multiCarousel1(this.carouselId);
		},
		methods: {
			onResize: function(x, y, width, height) {
				//console.log(width)				
				if(!width) {
					return;
				}
				this.style.width = width + "px";
				this.style.height = height + "px";
				//console.log(this.style);
				this.parentStyle.left = x;
				this.parentStyle.top = y;
				this.parentStyle.width = width;
				this.parentStyle.height = height;
				this.itemMdata.parentStyle = this.parentStyle;
			},
			defaultStop(e) {
				//console.log("dd")
				e.preventDefault();
			},
			removeComponent(i) {
				this.$emit('removeComponent', i);
			},
			onDrag: function(x, y) {
				this.itemMdata.parentStyle.left = x;
				this.itemMdata.parentStyle.top = y;
			}

		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.hove_con {
		position: relative;
		height: 100%;
		.edit_con {
			position: absolute;
			background: #FFFFFF;
			border-radius: 5px;
			box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.2);
			padding: 0 15px;
			top: -40px;
			width: auto;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				float: left;
				cursor: pointer;
				border-right: 1px solid #E3E3E3;
				margin: 0 8px;
			}
			span:nth-child(1) {
				padding-right: 7px;
				line-height: 25px;
				margin-top: 8px;
			}
			.icon {
				display: block;
				float: left;
				border-right: none;
				background: url(../../../static/image/bg03.png)no-repeat;
			}
			.icon i {
				position: absolute;
				display: block;
				font-size: 12px;
				width: auto;
				line-height: 25px;
				padding: 0px 10px;
				background: #FFFFFF;
				border-radius: 5px;
				box-shadow: 0 1px 4px 2px rgba(0, 0, 0, 0.1);
				top: -23px;
				margin-left: -25px;
				display: none;
			}
			.style {
				background-position: -60px -186px;
				width: 21px;
				height: 21px;
				margin-top: 10px;
			}
			.style:hover {
				background-position: -60px -232px;
			}
			.style:hover i {
				display: block;
			}
			.animation {
				background-position: -372px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.animation:hover {
				background-position: -372px -232px;
			}
			.animation:hover i {
				display: block;
			}
			.borderhid {
				background-position: -275px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.borderhid:hover {
				background-position: -275px -232px;
			}
			.borderhid:hover i {
				display: block;
			}
			.delete {
				background-position: -105px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.delete:hover {
				background-position: -105px -232px;
			}
			.delete:hover i {
				display: block;
			}
		}
	}
	
	.hove_con:hover {
		.edit_con {
			display: block;
		}
	}
</style>


