<template>
	
		<div class="multiCarousel hove_con">			
			
			<div class="carousel-wrap" :id="carouselId" :style="style">
				<ul class="slide-ul activeimg" :style="style">
					<li v-for="(list,index) in slideList" :key="index" :style="style">
						<img @mousedown="defaultStop($event)" :src="list.image" :alt="list.name">
					</li>
				</ul>
				<div class="bot-img">
					<ul>
						<li v-for="(list,index) in slideList" :key="index"><img @mousedown="defaultStop($event)" :src="list.image" :alt="list.name"></li>
						<!--li><img src="/static/image/img/2.jpg"></li>
						<li><img src="/static/image/img/3.jpg"></li>
						<li><img src="/static/image/img/4.jpg"></li>
						<li><img src="/static/image/img/5.jpg"></li-->
					</ul>
				</div>
				<div class="carousel-items" style="display: none;">
					<span v-for="(item,index) in slideList" :class="{'active':index===currentIndex}" @mouseover="change(index)"></span>
				</div>
			</div>
		</div>
	
</template>

<script>
	require("../../../../../static/js/emit.js");
	export default {
		name: 'multiCarousel',
		props: ['itemMdata', "posCom", 'index'],
		created() {
			//this.itemMdata.parentStyle.width = this.carouse;
			console.log(this.itemMdata);
			//debugger;
		},
		data() {
			return {
				title: "轮播多图",
				outerVisible: false,
				carouselId: "carousel" + this.posCom + this.index,
				width: 0,
				carouse: 300,
				height: 0,
				style: {
					width: this.itemMdata.parentStyle.width + 'px',
					height: this.itemMdata.parentStyle.height + 'px'
				},
				x: 0,
				y: 0,
				parentStyle: {

				},
				slideList: [{
						clickUrl: "#",
						name: "第1张",
						image: require("../../../../../static/image/img/1.jpg")
					},
					{
						clickUrl: "#",
						name: "第2张",
						image: require("../../../../../static/image/img/2.jpg")
					},
					{
						clickUrl: "#",
						name: "第3张",
						image: require("../../../../../static/image/img/3.jpg")
					},
					{
						clickUrl: "#",
						name: "第4张",
						image: require("../../../../../static/image/img/4.jpg")
					},
					{
						clickUrl: "#",
						name: "第5张",
						image: require("../../../../../static/image/img/5.jpg")
					}
				],
				currentIndex: 0,
				timer: '',
				editDial: {
					editDialWidth: "950px",
					editDialHeight: "680px",
					rTitle: '编辑轮播图'
				}
			}
		},
		mounted() {
			multiCarouse.multiCarousel1(this.carouselId);
		},
		methods: {
			onResize: function(x, y, width, height) {
				//console.log(width)				
				if(!width) {
					return;
				}
				this.style.width = width + "px";
				this.style.height = height + "px";
				//console.log(this.style);
				this.parentStyle.left = x;
				this.parentStyle.top = y;
				this.parentStyle.width = width;
				this.parentStyle.height = height;
				this.itemMdata.parentStyle = this.parentStyle;
			},
			defaultStop(e) {
				console.log("dd")
				e.preventDefault();
			},
			removeComponent(i) {
				this.$emit('removeComponent', i);
			},
			onDrag: function(x, y) {
				this.itemMdata.parentStyle.left = x;
				this.itemMdata.parentStyle.top = y;
			}

		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.carousel-wrap {
		position: relative;
		width: 100%;
		overflow: hidden;
		background-color: #fff;
		z-index: 99;
	}
	
	.slide-ul {
		width: 100%;
		position: absolute;
		height: 100%;
	}
	
	.slide-ul li {
		float: left;
	}
	
	.slide-ul li img {
		width: 100%;
		height: 100%;
	}
	
	.carousel-items {
		position: absolute;
		z-index: 10;
		bottom: 15px;
		width: 100%;
		margin: 0 auto;
		text-align: center;
		font-size: 0;
	}
	
	.carousel-items span {
		display: inline-block;
		height: 6px;
		width: 30px;
		margin: 0 3px;
		background-color: #FFFFFF;
		cursor: pointer;
	}
	
	.carousel-items .active {
		background-color: #2D64B3;
	}
	
	.bot-img {
		position: absolute;
		height: 60px;
		right: 10px;
		bottom: 10px;
	}
	
	.bot-img ul {
		height: 60px;
	}
	
	.bot-img li {
		float: right;
	}
	
	.bot-img li img {
		width: 60px;
		height: 60px;
	}
	
	.list-enter-to {
		transition: all 1s ease;
		transform: translateX(0);
	}
	
	.list-leave-active {
		transition: all 1s ease;
		transform: translateX(-100%);
	}
	
	.list-enter {
		transform: translateX(100%);
	}
	
	.list-leave {
		transform: translateX(0);
	}
	
	.hove_con {
		position: relative;
		height: 100%;
		.edit_con {
			position: absolute;
			background: #FFFFFF;
			border-radius: 5px;
			box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.2);
			padding: 0 15px;
			top: -40px;
			width: auto;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				float: left;
				cursor: pointer;
				border-right: 1px solid #E3E3E3;
				margin: 0 8px;
			}
			span:nth-child(1){
				padding-right: 7px;
				line-height: 25px;
				margin-top: 8px;
			}
			.icon{
				display: block;
				float: left;
				border-right: none;
				background: url(../../../../../static/image/bg03.png)no-repeat;
			}
			.icon i{
				position: absolute;
				display: block;
				font-size: 12px;
				width: auto;
				line-height: 25px;
				padding: 0px 10px;
				background: #FFFFFF;
			    border-radius: 5px;
			    box-shadow: 0 1px 4px 2px rgba(0, 0, 0, 0.1);
			    top: -23px;
			    margin-left: -25px;
			    display: none;
			}
			.style{
				background-position: -60px -186px;
				width: 21px;
				height: 21px;
				margin-top: 10px;
			}
			.style:hover{
				background-position: -60px -232px;
			}
			.style:hover i{
				display: block;
			}
			.animation{
				background-position: -372px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.animation:hover{
				background-position: -372px -232px;
			}
			.animation:hover i{
				display: block;
			}
			.borderhid{
				background-position: -275px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.borderhid:hover{
				background-position: -275px -232px;
			}
			.borderhid:hover i{
				display: block;
			}
			.delete{
				background-position: -105px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.delete:hover{
				background-position: -105px -232px;
			}
			.delete:hover i{
				display: block;
			}
		}
	}
	
	.hove_con:hover {
		.edit_con {
			display: block;
		}
	}
</style>


