<template>
	<div class="fluid container">
		<myHeader></myHeader>
		<div :class="['conect_inner',{'warning':isConLeave}]" style="min-height: 500px; border: 1px solid #fff;" @dragenter="dragConEnter($event)" @drop="onDragConDrop($event)" @dragleave="onDragConLeave($event)" @dragover="dragConover($event)">
			<div class="con_innd" v-dragresize>
				<div v-for="(i,tIndex) in state.itemCon[state.delayed].stream" class="g-cf in_outer">
					<!--自由容器-->
					<div v-if="i.tName=='freeContainer'" @dragover="onStreamOver($event,i)" @dragenter="onStreamEnter($event,i)" @drop.stop='inStreamDrop($event,i)' @dragleave="onStreamLeave($event,i)" :class="['in_inner resizeMe',{'enter':i.isStreamColumn}]" :style="i.conStyle">
						<div class="multi_edit f_hv"><span @click="i.isEdit = true">设置{{i.tText}}</span><span>隐藏边框</span><span @click="delModule(i)">删除模块</span></div>
						<div v-for="(i,index) in i.itemInner" :itemMdata="i" v-on:removeComponent="removeComponent" :posCom="'free'+tIndex" :index="index" :is="i.tName"></div>
						<uiDialog v-if="i.isEdit" :itemMdata='i'></uiDialog>
					</div>
					<!--多列排版-->
					<div v-if="i.tName=='multiColumnContainer'" :class="['in_inner resizeMe multi_con g-cf', {'enter':i.isStreamColumn}]" :style="i.conStyle">
						<div class="multi_edit f_hv"><span @click="i.isEdit = true">设置{{i.tText}}</span><span>隐藏边框</span><span @click="delModule(i)">删除模块</span></div>
						<div v-for="it in i.column" :class="['clm',{'enter':i.isStreamColumn},'clm'+i.column]" @dragover="onStreamOver($event,i,it)" @dragenter="onStreamEnter($event,i,it)" @drop.stop="inStreamDrop($event,i,it)" @dragleave="onStreamLeave($event,i,it)">
							<div v-for="(i,index) in i.itemInner">
								<div v-if="i.column==it" :itemMdata="i" :posCom="'multi'" :index="index" :is="i.tName"></div>
							</div>
						</div>
						<uiDialog v-if="i.isEdit" :itemMdata='i'></uiDialog>
					</div>
					<!--纵向标签-->
					<div v-if="i.tName=='longitudinalLabel'" @dragover="onStreamOver($event,i)" @dragenter="onStreamEnter($event,i)" @drop.stop='inStreamDrop($event,i)' @dragleave="onStreamLeave($event,i)" :class="['in_inner resizeMe',{'enter':i.isStreamColumn}]" :style="i.conStyle">
						<div class="multi_edit f_hv"><span @click="i.isEdit = true">设置{{i.tText}}</span><span>隐藏边框</span><span @click="delModule(i)">删除模块</span></div>
						<div class="g-cf longitud">
							<div class="tit_con">
								<ul>
									<li><span>标签1</span></li>
									<li><span>标签2</span></li>
								</ul>
							</div>
							<div class="long_con">
								<div class="edtem-label long_i1">
									标签1内容
								</div>
								<div class="edtem-label long_i2">
									标签2内容
								</div>
							</div>
						</div>
						<uiDialog v-if="i.isEdit" :itemMdata='i'></uiDialog>
					</div>
					<!--横向标签-->
					<div v-if="i.tName=='ateralLabelContainer'" @dragover="onStreamOver($event,i)" @dragenter="onStreamEnter($event,i)" @drop.stop='inStreamDrop($event,i)' @dragleave="onStreamLeave($event,i)" :class="['in_inner',{'enter':i.isStreamColumn}]" :style="i.conStyle">
						<div class="multi_edit f_hv"><span @click="i.isEdit = true">设置{{i.tText}}</span><span>隐藏边框</span><span @click="delModule(i)">删除模块</span></div>
						<div class="ateralLabel">
							<div class="tit_con">
								<ul class="g-cf">
									<li><span>标签1</span></li>
									<li><span>标签2</span></li>
								</ul>
							</div>
							<div class="long_con">
								<div class="edtem-label long_i1">
									标签1内容
								</div>
								<div class="edtem-label long_i2">
									标签2内容
								</div>
							</div>
						</div>
						<uiDialog v-if="i.isEdit" :itemMdata='i'></uiDialog>
					</div>
					<!--通栏排版-->
					<div v-if="i.tName=='columnLayout'" :class="['in_inner columnLayout', {'enter':i.isStreamColumn}]">
						<div :style="i.conStyle" @dragover="onStreamOver($event,i)" @dragenter="onStreamEnter($event,i)" @drop.stop='inStreamDrop($event,i)' @dragleave="onStreamLeave($event,i)" :class="['in_inner',{'enter':i.isStreamColumn}]">
							<div class="multi_edit f_hv"><span @click="i.isEdit = true">设置{{i.tText}}</span><span>隐藏边框</span><span @click="delModule(i)">删除模块</span></div>
							<div v-for="(i,index) in i.itemInner" :itemMdata="i" :removeComponent="removeComponent" :posCom="'free'" :index="index" :is="i.tName"></div>
						</div>
						<uiDialog v-if="i.isEdit" :itemMdata='i'></uiDialog>
					</div>
				</div>
				<!--添加新容器-->
				<div :class="{'enter':isAddNew}" @dragenter="onAddNewEnter($event)" @drop.stop='inAddNewDrop($event)' @dragover='inAddNewOver($event)' class="g-cf in_outer addMern">
					<div class="dragNew">
						<p>添加模块</p>
						<span>可以拖模块进来</span>
					</div>
				</div>
			</div>
		</div>
		<div class="pos_con">
			<div v-for="(i,index) in state.itemCon[state.delayed].posCom" class="g-cf in_outer">
				<div :itemMdata="i" :posCom="'posCom'" :index="index" :is="i.tName"></div>
			</div>
		</div>
		<myFooter></myFooter>
	</div>
</template>

<script>
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	import VueDraggableResizable from 'vue-draggable-resizable'
	import myHeader from './header.vue'
	import myFooter from './footer.vue'
	import mFont from '../m/font.vue'
	import mImage from '../m/mImage.vue'
	import mButton from '../m/mButton.vue'
	import graphic from '../m/graphic.vue'
	import columnPicture from '../m/columnPicture.vue'
	import uiDialog from '../m/uiDialog.vue'
	import multiCarousel from '../m/multiCarousel.vue'
	import articleScrap from '../m/articleScrap.vue'
	import moduleAreaNav from '../m/topGrade/moduleAreaNav.vue'
	import photoMoreCard from '../m/topGrade/photoMoreCard.vue'

	export default {
		name: 'count',
		components: {
			myHeader,
			myFooter,
			uiDialog,
			moduleAreaNav,
			photoMoreCard,
			VueDraggableResizable,
			columnPicture,
			multiCarousel,
			mFont,
			articleScrap,
			graphic,
			mImage,
			mButton
		},
		props: ['tm', 'state'],
		updated: function() {
			this.$nextTick(function() {
				console.log("$nextTick")
			})
		},
		created() {
			var temp = this.$route.params.param;
			//this.setDelayed(temp);
			//this.state.delayed = temp;
			console.log(this.state)
		},
		data() {
			return {
				isMultiColumn: false,
				isConLeave: false,
				x: 0,
				y: 'y',
				isAddNew: false
			}
		},
		methods: {
			onDrop: function(event) {
				event.preventDefault();
				var dt = this;
				if(!dt.state.curOrder) return;
				var obj = dt.state.curOrder;
				var itemCon = dt.state.itemCon[dt.state.delayed];
				if(itemCon) {
					for(var i = 0; itemCon.length > i; i++) {
						itemCon[i].cur = false;
					}
				}
				var item = JSON.parse(JSON.stringify(obj));
				item.order = itemCon.length + 1;
				item.cur = true;
				this.pushItem(item);
				dt.state.curOrder = null;
			},
			onDragConLeave(event) {
				this.isConLeave = true;
				console.log("离开外层标准流容器");
			},
			dragConEnter(event) {
				console.log("进入外层标准流容器");
			},
			onDragConDrop(event) {
				this.isConLeave = false;
				event.preventDefault();
				var dt = this;
				if(!dt.state.curOrder) return;
				var obj = dt.state.curOrder;
				var itemCon = dt.state.itemCon[dt.state.delayed].posCom;
				if(itemCon) {
					for(var i = 0; itemCon.length > i; i++) {
						itemCon[i].cur = false;
					}
				}
				var item = JSON.parse(JSON.stringify(obj));
				item.cur = true;
				dt.state.itemCon[dt.state.delayed].posCom.push(item);
				dt.state.curOrder = null;
				console.log("拖入外层标准流容器");
			},
			dragConover(event, i) {
				event.preventDefault();
				this.isConLeave = false;
				console.log("进入外层标准流容器(移动阶段 )");
			},
			onStreamEnter(event, i) {
				console.log("进入标准流容器");
				console.log(event.target.height);
				event.preventDefault();
			},
			onStreamOver(event, i) {
				i.isStreamColumn = true;
				console.log("进入标准流容器(移动阶段 )");
			},
			onStreamLeave(event, i) {
				i.isStreamColumn = false;
				console.log("离开标准流容器标准流容器");
			},
			inStreamDrop(event, i, it) {
				console.log("拖入" + i.tText);
				event.preventDefault();
				i.isStreamColumn = false;
				var dt = this;
				if(!dt.state.curOrder) return;
				var obj = dt.state.curOrder;
				var itemCon = [];
				var stream = dt.state.itemCon[dt.state.delayed].stream;
				for(let j = 0; stream.length > j; j++) {
					if(stream[j].tName == i.tName) {
						itemCon = stream[j].itemInner;
					}
				}
				if(itemCon) {
					for(var j = 0; itemCon.length > j; j++) {
						itemCon[j].cur = false;
					}
				}
				var item = JSON.parse(JSON.stringify(obj));
				item.order = itemCon.length + 1;
				item.cur = true;
				item.parent = true;
				item.column = it || -1;
				itemCon.push(item);
				dt.state.curOrder = null;
			},
			onAddNewEnter(event) {
				console.log("进入新增容器(进入阶段 )");
				event.preventDefault();
			},
			inAddNewDrop(event) {
				//this.cmName = true;
				console.log("拖入新增容器");
				event.preventDefault();
				var dt = this;
				if(!dt.state.curOrder) return;
				var item = {};
				if(dt.state.curOrder.mClass != "Typesetting") {
					item = {
						tName: "freeContainer",
						tText: "自由容器",
						isEdit: false,
						mClass: "Typesetting",
						isStreamColumn: false,
						conStyle: {
							width: '990px',
							margin: "0 auto",
							minHeight: "100px"
						},
						order: 1,
						itemInner: [dt.state.curOrder],
						cur: true
					}
				} else {
					item = dt.state.curOrder;
				}
				console.log(item);
				item = JSON.parse(JSON.stringify(item));
				dt.state.itemCon[dt.state.delayed].stream.push(item);
				dt.state.curOrder = null;
			},
			removeComponent(comp) {
				var stream = this.state.itemCon[this.state.delayed].stream;
				
				for(var j = 0; stream.length > j; j++) {
					var itemInner = stream[j].itemInner;
					itemInner.splice(itemInner.indexOf(comp), 1);
				}
			},
			delModule(item) {
				var stream = this.state.itemCon[this.state.delayed].stream;
				//console.log("stream1:"+stream.length);		
				stream.splice(stream.indexOf(item), 1);
				this.orderbyId();
				//console.log("stream2:"+stream.length);
				//this.state.itemCon[this.state.delayed].stream.$remove(i);
			},
			inAddNewOver(event) {
				event.preventDefault();
				console.log("进入新增容器(移动阶段 )");
			},
			getDart(v) {
				console.log(v);
			},
			...mapMutations([
				'orderbyId','setDelayed'
			])
		},
		
		
	}
</script>

<style lang="scss" scoped>
	.container .conect_inner {
		width: 990px;
		background-color: #fff;
		position: relative;
		margin: 0 auto;
		.columnLayout {
			position: relative;
			margin-left: -45%;
			margin-right: -45%;
		}
	}
	
	.in_outer {
		position: relative;
	}
	
	.container .warning {
		border: 1px dashed red;
	}
	
	.in_inner {
		border: 1px dashed #fff;
		position: relative;
		.del_icon {
			text-align: left;
		}
		.multi_edit {
			position: absolute;
			top: -40px;
			width: 450px;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				cursor: pointer;
				margin-right: 10px;
			}
		}
	}
	
	.in_inner:hover {
		border-color: #ccc;
		.multi_edit {
			display: block;
		}
	}
	
	.multi_con {
		.clm {
			border: 1px dashed #fff;
			float: left;
			position: relative;
		}
		.clm2 {
			width: 50%;
		}
		.clm3 {
			width: 33.13%;
		}
		.clm4 {
			width: 25%;
		}
		.clm5 {
			width: 20%;
		}
	}
	
	.longitud {
		.tit_con {
			float: left;
			width: 20%;
			ul {}
		}
		.long_con {
			float: left;
			width: 80%;
		}
	}
	
	.ateralLabel {
		ul {
			li {
				float: left;
				width: 100px;
				text-align: center;
			}
		}
		.long_con {}
	}
	
	.container .enter {
		border: #5874d8 1px dashed;
	}
	
	.addMern {
		width: 990px;
		margin: 0 auto;
		.dragNew {
			min-height: 100px;
			padding-top: 35px;
			text-align: center;
			background-color: rgba(204, 204, 204, 0.36);
			p {
				text-align: center;
				cursor: pointer;
				width: 150px;
				margin: 0 auto;
				line-height: 30px;
				height: 30px;
				background-color: #ccc;
				border: 1px solid #666;
				border-radius: 4px;
			}
		}
	}
</style>


