<template>
	<div class="carEdit">
		<ul class="left">
			<li @click="changetype(1)" :class="{active:typenumber == 1}">常规</li>
			<li @click="changetype(2)" :class="{active:typenumber == 2}">轮播设置</li>
			<li @click="changetype(3)" :class="{active:typenumber == 3}">高级</li>
		</ul>
		<div class="right">
			<div class="one" v-if="typenumber == 1">
				<!--p class="title">
					<span>模块标题：</span>
					<input type="text" value="banner" />
				</p-->
				<div class="style">
					<p>切换样式：</p>
					<ul class="list">
						<li @click="selctCurFun(item,index)" v-for="(item,index) in selctCur">
							<label :class="{'active':item.cur}">
							    <input type="radio" name="style" v-model="item.mValue" />
							    <div :class="[item.mClass]"></div>
						    </label>
							<p>{{item.mText}}</p>
						</li>
						<!--li :class="">
							<label>
							    <input type="radio" name="style" />
							    <div class="style_two"></div>
						    </label>
							<p>圆点切换</p>
						</li>
						<li :class="">
							<label>
							    <input type="radio" name="style" v-model="1" />
							    <div class="style_three"></div>
						    </label>
							<p>方块切换</p>
						</li>
						<li :class="">
							<label>
							    <input type="radio" name="style" v-model="1" />
							    <div class="style_four"></div>
						    </label>
							<p>箭头切换</p>
						</li-->
					</ul>
				</div>
				<div class="page">
					<p>轮播页数：</p>
					<el-select v-model="value" placeholder="请选择">
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="speed">
					<p>轮播速度：</p>
					<el-select v-model="value2" placeholder="请选择">
						<el-option v-for="item in speed" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="two" v-if="typenumber == 2">
				<table>
					<tr>
						<th width="40%">图片</th>
						<th width="30%">页数</th>
						<th width="30%">操作</th>
					</tr>
					<tr v-for="(item,index) in itemMdata.slideList">
						<td>
							<img @click="mDataSelt(item)" :src="item.url" :alt="item.name" />
						</td>
						<td>
							<p>第{{index+1}}页</p>
						</td>
						<td>
							<p class="edit" @click="imgedit(item)"></p>							
						</td>
					</tr>
					<!--tr>
						<td>
							<img src="../../../static/image/default.png" />
						</td>
						<td>
							<p>第2页</p>
						</td>
						<td>
							<p class="edit" @click="imgedit"></p>
						</td>
					</tr>
					<tr>
						<td>
							<img src="../../../static/image/default.png" />
						</td>
						<td>
							<p>第3页</p>
						</td>
						<td>
							<p class="edit" @click="imgedit"></p>
						</td>
					</tr-->
				</table>
				<uiLink :itemMdata='item' :editDial="editDial"></uiLink>			
				<el-dialog title="添加图片" :visible.sync="flag" width="60%">
					<div class="g-cf add_nav_cons">
						<div class="list_con">
							<mImageEdit :itemMdata="item"></mImageEdit>
						</div>
					</div>
					<span slot="footer" class="dialog-footer">
				    <el-button @click="flag = false">取 消</el-button>
				    <el-button type="primary" @click="flag = false">保 存</el-button>
				  </span>
				</el-dialog>
				<div class="img_edit" v-show="img_edit">
					<div class="top">
						<p>设置图片</p>
						<p @click="img_edit = !img_edit">X</p>
					</div>
					<div class="con_rt">
						<el-button>上传按钮</el-button>
						<div class="inner">
							<img src="../../../static/image/default.png" />
						</div>
					</div>
					<!--el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList2" list-type="picture">
						<el-button size="small" type="primary">点击上传</el-button>
						<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过3M</div>
					</el-upload-->
				</div>
			</div>
			<div class="three" v-if="typenumber == 3">
				<div class="list">
					<p>通栏边框：</p>
					<p @click="border_hid = false">
						<input type="radio" name="border" id="border1" />
						<label for="border1">隐藏</label>
					</p>
					<p @click="border_hid = true">
						<input type="radio" name="border" id="border2" checked="checked" />
						<label for="border2">显示</label>
					</p>
					<div class="border_hid" v-show="border_hid">
						<p>
							<span>宽度：</span>
							<input type="text" value="1" />
						</p>
						<p>
							<span>样式：</span>
							<el-select v-model="value3" placeholder="请选择">
								<el-option v-for="item in lines" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
						</p>
						<p>
							<span>颜色：</span>
							<div class="block">
								<el-color-picker v-model="color1"></el-color-picker>
							</div>
						</p>
					</div>
				</div>
				<div class="list">
					<p>内容边距：</p>
					<p>
						<input type="radio" name="margin" id="margin1" checked="checked" />
						<label for="margin1">默认</label>
					</p>
					<p>
						<input type="radio" name="margin" id="margin2" />
						<label for="margin2">自定义</label>
					</p>
				</div>
				<div class="list">
					<p>内容宽度：</p>
					<p>
						<input type="radio" name="width" id="width1" checked="checked" />
						<label for="width1">默认</label>
					</p>
					<p>
						<input type="radio" name="width" id="width2" />
						<label for="width2">自定义</label>
					</p>
				</div>
				<div class="list">
					<p>轮播设置：</p>
					<p>
						<input type="radio" name="play" id="play1" checked="checked" />
						<label for="play1">手动轮播</label>
					</p>
					<p>
						<input type="radio" name="play" id="play2" />
						<label for="play2">自动轮播</label>
					</p>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	import uiLink from './uiLink.vue'
	import mImageEdit from './mImageEdit.vue'
	export default {
		name: 'multiCarouselEdit',
		props: ['itemMdata'],
		created() {
			console.log(this.itemMdata);
		},
		components: {
			uiLink,
			mImageEdit
		},
		data() {
			return {
				title: "编辑轮播多图",
				typenumber:'1',
				options: [{
					value: '选项1',
					label: '2'
				}, {
					value: '选项2',
					label: '3'
				}, {
					value: '选项3',
					label: '4'
				}, {
					value: '选项4',
					label: '5'
				}],
				value: '',
				speed: [{
					value: '1',
					label: '1秒'
				}, {
					value: '1.5',
					label: '1.5秒'
				}, {
					value: '2',
					label: '2秒'
				}, {
					value: '3.5',
					label: '3.5秒'
				}],
				value2: '',
				editDial: {
					artTitle: "设置链接地址",
					setlink: false
				},
				item:[],
				fileList2: [{
					name: 'food.jpeg',
					url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
				}, {
					name: 'food2.jpeg',
					url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
				}],
				img_edit: false,
				flag:false,
				color1: '#409EFF',
				selctCur: [{
					mClass: 'style_one',
					mText: "数字切换",
					cur: true,
					mValue: 1
				}, {
					mClass: 'style_two',
					mText: "圆点切换",
					cur: false,
					mValue: 2
				}, {
					mClass: 'style_three',
					mText: "方块切换",
					cur: false,
					mValue: 3
				}, {
					mClass: 'style_four',
					mText: "箭头切换",
					cur: false,
					mValue: 4
				}],
				lines: [{
					value: '选项1',
					label: '实线'
				}, {
					value: '选项2',
					label: '虚线'
				}, {
					value: '选项3',
					label: '点线'
				}],
				value3: '',
				border_hid: true
			}
		},
		methods: {
			changetype:function(number){
				this.typenumber = number
			},
			selctCurFun(item, index) {
				for(var g = 0; g < this.selctCur.length; g++) {
					this.selctCur[g].cur = false;
				}
				item.cur = true;
				this.itemMdata.curIndex = index;
				//console.log(item);
			},
			border: function() {

			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
			mDataSelt(i){
				this.item = i;
				this.flag = true;
			},
			imgedit: function(i) {
				this.item = i;
				this.editDial.setlink = true;
			}
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.carEdit {
		clear: both;
		overflow: hidden;
		.left {
			display: block;
			float: left;
			width: 15%;
			height: 530px;
			border-right: 1px solid #CCCCCC;
			li {
				box-sizing: border-box;
				width: 100%;
				height: 60px;
				line-height: 60px;
				padding-left: 35px;
			}
			li:nth-child(1),
			li:nth-child(2) {
				border-bottom: 1px solid #CCCCCC;
			}
			.active {
				width: 144px;
				border-bottom: 1px solid #CCCCCC;
				border-left: 5px solid #409EFF;
				border-right: 1px solid #FFFFFF;
				color: #409EFF;
			}
		}
		.right {
			position: relative;
			box-sizing: border-box;
			float: left;
			width: 84.8%;
			height: 530px;
			padding: 30px;
			.one .title {
				clear: both;
				overflow: hidden;
				height: 42px;
				input {
					height: 40px;
					width: 220px;
					padding-left: 10px;
					border: 1px solid #E3E3E3;
				}
				input:hover,
				input:focus {
					border: 1px solid #409EFF;
				}
			}
			.one .style {
				clear: both;
				overflow: hidden;
				margin-top: 30px;
				p {
					float: left;
				}
				.list {
					float: left;
					width: 90%;
					li {
						float: left;
						width: 18%;
						margin-left: 5px;
						margin-right: 5px;
						label {
							display: block;
							width: 100%;
							border: 1px solid #E3E3E3;
							height: 43px;
							position: relative;
							cursor: pointer;
							div {
								width: 90px;
								height: 25px;
								background-image: url(../../../static/image/mbg01.png);
								background-repeat: no-repeat;
								margin-left: 13px;
								margin-top: 10px;
							}
							.style_one {
								background-position: -875px -1282px;
							}
							.style_two {
								background-position: -875px -1345px;
							}
							.style_three {
								background-position: -875px -1404px;
							}
							.style_four {
								background-position: -875px -1214px;
							}
							input {
								position: absolute;
								top: 0;
								bottom: 0;
								left: 0;
								right: 0;
								opacity: 0;
								cursor: pointer;
							}
						}
						label:hover {
							border: 1px solid #409EFF;
						}
						.active {
							border: 1px solid #409EFF;
						}
						p {
							float: none;
							text-align: center;
							line-height: 30px;
							font-size: 13px;
						}
					}
				}
			}
			.one .page,
			.one .speed {
				clear: both;
				overflow: hidden;
				margin-top: 30px;
				p {
					float: left;
					margin-right: 5px;
				}
			}
			.two table {
				width: 100%;
				border: 1px solid #E3E3E3;
				th {
					background: #f8f9fb;
					height: 45px;
				}
				td {
					text-align: center;
					padding: 15px 0;
					img {
						max-width: 60%;
						height: 50px;
					}
					.edit {
						width: 20px;
						height: 20px;
						background-image: url(../../../static/image/mbg01.png);
						background-repeat: no-repeat;
						background-position: -405px -60px;
						margin-left: 45%;
						cursor: pointer;
					}
				}
				tr:nth-child(2n-1) {
					background: #f8f9fb;
				}
			}
			.two .img_edit {
				position: absolute;
				top: 50px;
				left: 90px;
				right: 90px;
				bottom: 20px;
				background: #FFFFFF;
				box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.2);
				border-radius: 5px;
				.top {
					border-bottom: 1px solid #E3E3E3;
					padding: 10px 15px;
					clear: both;
					overflow: hidden;
					p:nth-child(1) {
						float: left;
						font-size: 16px;
					}
					p:nth-child(2) {
						float: right;
						font-size: 16px;
					}
				}
				.upload-demo {
					padding: 25px;
				}
			}
			.three .list {
				clear: both;
				overflow: hidden;
				margin-top: 20px;
				p {
					float: left;
					margin-right: 15px;
				}
				.border_hid {
					float: left;
					margin-top: 40px;
					margin-left: -50px;
					p {
						float: none;
						margin-bottom: 10px;
						input {
							border: 1px solid #E3E3E3;
							padding-left: 10px;
							line-height: 35px;
						}
						span {
							float: left;
						}
					}
					.block {
						float: left;
					}
				}
			}
		}
	}
</style>


