<template>
	<vue-draggable-resizable :x="router.parentStyle.left" :y="router.parentStyle.top" :w="router.parentStyle.width" :h="router.parentStyle.height||30" :parent="itemMdata.parent" v-on:dragging="onDrag" v-on:resizing="onResize" :drag-cancel="'.cancel'">
		<div class="moduleAreaNav hove_con">
			<p class="edit_con"><span @click="itemMdata.isEdit = true">管理导航</span> | <i class="icon special_effect">模块特效</i> <i class="icon picture_shape">图片形状</i><i class="icon module_animation">模块动画</i><i class="icon">设置透明度</i>
			</p>
			<div class="moduleAreaNav_con">
				<ul class="g-cf nav_i_show0">
					<li class="nav_lifirth" v-for="item in router.navItem">
						<div class="tit_i1">{{item.tText}}</div>
						<ul class="g-cf sec_con">
							<li v-for="second in item.secondNav">
								<div class="tit_i2">{{second.tText}}</div>
								<div class="third_con">
									<div class="third_item" v-for="third in second.thirdNav">{{third.tText}}</div>
								</div>
							</li>
						</ul>
					</li>
				</ul>
			</div>
			<uiDialog v-if="itemMdata.isEdit" :itemMdata='itemMdata' :editDial="editDial"></uiDialog>
		</div>
	</vue-draggable-resizable>
</template>
<script>
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	import uiDialog from '../uiDialog.vue'
	import VueDraggableResizable from 'vue-draggable-resizable'
	export default {
		name: 'moduleAreaNav',
		components: {
			uiDialog,
			VueDraggableResizable
		},
		props: ['itemMdata'],
		created() {
			console.log(this.router.navItem);		
		},
		methods: {
			removeComponent(i) {
				this.$emit('removeComponent', i); //删除该项
			},
			onResize: function(x, y, width, height) {
				this.parentStyle.left = x;
				this.parentStyle.top = y;
				this.parentStyle.width = width;
				this.parentStyle.height = height;
				this.router.parentStyle = this.parentStyle;
			},
			onDrag: function(x, y) {
				this.router.parentStyle.left = x;
				this.router.parentStyle.top = y;
			}
		},
		computed: {
			...mapGetters(["router"])
		},
		data() {
			return {
				title: "栏目导航",
				cons: "图片内容",
				parentStyle: {

				},
				editDial: {
					editDialWidth: "650px",
					editDialHeight: "580px",
					rTitle: '管理栏目'
				},
			}
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.hove_con {
		position: relative;
		z-index: 9;
		.edit_con {
			position: absolute;
			top: -40px;
			width: 500px;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				cursor: pointer;
			}
		}
	}
	
	.hove_con:hover {
		.edit_con {
			display: block;
		}
	}
</style>


