<template>
	<div class="photoMoreCard">
		<div class="mCon">
			<div class="styleone clearfloat">
				<div class="oneLeft fl"></div>
				<div class="oneRight fl">
					<div class="oneRTop"></div>
					<div class="oneRBottom">
						<div class="oneBLeft fl"></div>
						<div class="oneBRight fl"></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'photoMoreCard',
		props: ['itemMdata'],
		created() {
			console.log(this.itemMdata);
		},
		methods: {
			removeComponent(i) {
				//this.count = Number(this.count) + 1;
				//this.reversed = this.count + this.fdlag;
				this.$emit('removeComponent', i);
			}
		},
		data() {
			return {
				title: "魔方多图",
				cons: "图片内容"
			}
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.clearfloat:after{display:block;clear:both;content:"";visibility:hidden;height:0} 
	.fl{float:left;}
	.styleone{
		height:100px;
		.oneLeft{width:30px;border:1px solid #ccc;}
		.oneRight{
			margin-left:1px;
			width:69px;
			border:1px solid #ccc;
			.oneRTop{
				height:60px;
				border:1px solid #ccc;
			}
			.oneRBottom{
				height:39px;
				margin-top:1px;
				.oneBLeft{width:50px;border:1px solid #ccc;}
				.oneBRight{width:50px;border:1px solid #ccc;}
			}
		}
	}
	.button {
		text-align: center;
	}
	
	button {
		padding: 3px 6px;
		color: #fff;
		background-color: rgb(52, 182, 253);
	}
	
</style>


