<template>
	
		<div class="serviceOnline hove_con">
			
			<div class="detail">
				<div class="kefu">
					<p class="title">在线客服</p>
					<ul>
						<li class="text" v-for="table in itemMdata.serviceOnlineTable" :style="{float:itemMdata.float}">
							<img :src="itemMdata.icon"></img>
							<span>{{table.name}}</span>
						</li>
					</ul>
				</div>
				<div class="time">
					<p class="title">工作时间</p>
					<p class="text">
						<span>{{itemMdata.time1}}至{{itemMdata.time2}}</span>
						<span>{{itemMdata.time3}}-{{itemMdata.time4}}</span>
					</p>
				</div>
			</div>
		</div>
	
</template>

<script>
	export default {
		name: 'serviceOnline',
		props: ['itemMdata'],
		created() {
			console.log(this.itemMdata);
		},
		data() {
			return {
				title: "在线客服",
				outerVisible: false,
				dialogVisible: false,
				innerVisible: false,
				parentStyle: {

				},
				editDial: {
					editDialWidth: "650px",
					editDialHeight: "600px",
					rTitle: '编辑在线客服',
				}
			}
		},
		methods: {
			onResize: function(x, y, width, height) {
				this.parentStyle.left = x;
				this.parentStyle.top = y;
				this.parentStyle.width = width;
				this.parentStyle.height = height;
				this.itemMdata.parentStyle = this.parentStyle;
			},
			removeComponent(i) {
				this.$emit('removeComponent', i);
			},
			onDrag: function(x, y) {
				this.itemMdata.parentStyle.left = x;
				this.itemMdata.parentStyle.top = y;
			}
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.hove_con {
		position: relative;
		.edit_con {
			position: absolute;
			background: #FFFFFF;
			border-radius: 5px;
			box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.2);
			padding: 0 15px;
			top: -40px;
			width: auto;
			height: 40px;
			line-height: 40px;
			display: none;
			span {
				float: left;
				cursor: pointer;
				border-right: 1px solid #E3E3E3;
				margin: 0 8px;
			}
			span:nth-child(1){
				padding-right: 7px;
				line-height: 25px;
				margin-top: 8px;
			}
			.icon{
				display: block;
				float: left;
				border-right: none;
				background: url(../../../../../static/image/bg03.png)no-repeat;
			}
			.icon i{
				position: absolute;
				display: block;
				font-size: 12px;
				width: auto;
				line-height: 25px;
				padding: 0px 10px;
				background: #FFFFFF;
			    border-radius: 5px;
			    box-shadow: 0 1px 4px 2px rgba(0, 0, 0, 0.1);
			    top: -23px;
			    margin-left: -25px;
			    display: none;
			}
			.style{
				background-position: -60px -186px;
				width: 21px;
				height: 21px;
				margin-top: 10px;
			}
			.style:hover{
				background-position: -60px -232px;
			}
			.style:hover i{
				display: block;
			}
			.animation{
				background-position: -372px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.animation:hover{
				background-position: -372px -232px;
			}
			.animation:hover i{
				display: block;
			}
			.borderhid{
				background-position: -275px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.borderhid:hover{
				background-position: -275px -232px;
			}
			.borderhid:hover i{
				display: block;
			}
			.delete{
				background-position: -105px -186px;
				width: 21px;
				height: 20px;
				margin-top: 10px;
			}
			.delete:hover{
				background-position: -105px -232px;
			}
			.delete:hover i{
				display: block;
			}
		}
		.detail {
			p {
				clear: both;
				overflow: hidden;
			}
			.title {
				font-weight: bold;
				border-bottom: 1px dashed #E3E3E3;
				font-size: 16px;
				color: #888888;
				line-height: 35px;
				margin-top: 10px;
			}
			ul {
				clear: both;
				overflow: hidden;
				li{
					width: auto;
				}
			}
			.text {
				color: #888888;
				line-height: 30px;
				margin-left: 20px;
				img {
					margin-right: 5px;
				}
				span {
					margin-right: 10px;
				}
			}
		}
	}
	
	.hove_con:hover {
		.edit_con {
			display: block;
		}
	}
</style>


