<script>
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	import VueDraggableResizable from 'vue-draggable-resizable'
	import myHeader from './header.vue'
	import myFooter from './footer.vue'
	import mFont from '../m/font.vue'
	import mImage from '../m/mImage.vue'
	import mButton from '../m/mButton.vue'
	import graphic from '../m/graphic.vue'
	import columnPicture from '../m/columnPicture.vue'
	import uiDialog from '../m/uiDialog.vue'
	import multiCarousel from '../m/multiCarousel.vue'
	import articleScrap from '../m/articleScrap.vue'
	import moduleAreaNav from '../m/topGrade/moduleAreaNav.vue'
	import photoMoreCard from '../m/topGrade/photoMoreCard.vue'
	import productShow from '../m/productShow.vue'
	import productSearch from '../m/productSearch.vue'
	import msgSubmit from '../m/msgSubmit.vue'
	import siteForm from '../m/siteForm.vue'
	import serviceOnline from '../m/serviceOnline.vue'
	import areaFile from '../m/areaFile.vue'
	import areaMap from '../m/areaMap.vue'
	import areaLocation from '../m/areaLocation.vue'
	import areaIndexFavorite from '../m/areaIndexFavorite.vue'
	import siteQrCode from '../m/siteQrCode.vue'
	import areaFlv from '../m/areaFlv.vue'
	import productNav from '../m/productNav.vue'
	import newlyShop from '../m/newlyShop.vue'
	import areaCode from '../m/areaCode.vue'
	import areaDate from '../m/areaDate.vue'
	import memberLogin from '../m/memberLogin.vue'

	export default {
		name: 'xIndex',
		components: {
			myHeader,
			myFooter,
			memberLogin,
			uiDialog,
			moduleAreaNav,
			photoMoreCard,
			serviceOnline,
			VueDraggableResizable,
			columnPicture,
			multiCarousel,
			mFont,
			articleScrap,
			graphic,
			mImage,
			mButton,
			productShow,
			productSearch,
			msgSubmit,
			siteForm,
			serviceOnline,
			areaFile,
			areaMap,
			areaDate,
			areaLocation,
			areaIndexFavorite,
			siteQrCode,
			areaFlv,
			productNav,
			areaCode,
			newlyShop
		},
		updated: function() {

		},
		created() {
			this.getData(this.id);
			var temp = this.$route.params.param;
			this.setDelayed(temp);
			//console.log(this.state);
			//debugger;
		},
		data() {
			return {
				isMultiColumn: false,
				isConLeave: false,
				id: this.$route.params.id,
				x: 0,
				y: 'y',
				isAddNew: false
			}
		},
		methods: {
			onDrop: function(event) {
				event.preventDefault();
				var dt = this;
				if(!dt.state.curOrder) return;
				var obj = dt.state.curOrder;
				var itemCon = dt.state.itemCon[dt.state.delayed];
				if(itemCon) {
					for(var i = 0; itemCon.length > i; i++) {
						itemCon[i].cur = false;
					}
				}
				var item = JSON.parse(JSON.stringify(obj));
				item.order = itemCon.length + 1;
				item.cur = true;
				this.pushItem(item);
				dt.state.curOrder = null;
			},
			getDataFree(v) {
				console.log(v);
				console.log(this.state.itemCon[this.state.delayed].stream[v.el].conStyle)
				this.state.itemCon[this.state.delayed].stream[v.el].conStyle.height = v.height;
			},
			onDragConLeave(event) {
				this.isConLeave = true;
				console.log("离开外层标准流容器");
			},
			dragConEnter(event) {
				console.log("进入外层标准流容器");
			},
			onDragConDrop(event) {
				this.isConLeave = false;
				event.preventDefault();
				var dt = this;
				if(!dt.state.curOrder) return;
				var obj = dt.state.curOrder;
				var itemCon = dt.state.itemCon[dt.state.delayed].posCom;
				if(itemCon) {
					for(var i = 0; itemCon.length > i; i++) {
						itemCon[i].cur = false;
					}
				}
				obj.parent = false;
				var item = JSON.parse(JSON.stringify(obj));
				item.cur = true;
				dt.state.itemCon[dt.state.delayed].posCom.push(item);
				dt.state.curOrder = null;
				console.log("拖入外层标准流容器");
			},
			dragConover(event, i) {
				event.preventDefault();
				this.isConLeave = false;
				console.log("进入外层标准流容器(移动阶段 )");
			},
			onStreamEnter(event, i) {
				console.log("进入标准流容器");
				$(".multi_con").each(function(v) {
					var h = $(this).height();
					$(this).find(".clm").height(h);
				})
				event.preventDefault();
			},
			onStreamOver(event, i) {
				i.isStreamColumn = true;
				console.log("进入标准流容器(移动阶段 )");
			},
			onStreamLeave(event, i) {
				i.isStreamColumn = false;
				console.log("离开标准流容器标准流容器");
			},
			inStreamDrop(event, i, it) {
				console.log("拖入" + i.tText);
				event.preventDefault();
				i.isStreamColumn = false;
				var dt = this;
				if(!dt.state.curOrder) return;
				var obj = dt.state.curOrder;
				var itemCon = [];
				var stream = dt.state.itemCon[dt.state.delayed].stream;
				/*for(let j = 0; stream.length > j; j++) {
					if(stream[j].tName == i.tName) {
						itemCon = stream[j].itemInner;
					}
				}*/

				var item = JSON.parse(JSON.stringify(obj));
				item.order = itemCon.length + 1;
				if(obj.mClass == "Typesetting") {
					stream.push(obj);
					return;
				}
				item.cur = true;
				item.parent = true;
				item.column = it || -1;
				i.itemInner.push(item);
				dt.state.curOrder = null;
			},
			onAddNewEnter(event) {
				console.log("进入新增容器(进入阶段 )");
				event.preventDefault();
			},
			inAddNewDrop(event) {
				console.log("拖入新增容器");
				event.preventDefault();
				var dt = this;
				if(!dt.state.curOrder) return;
				var item = {};
				var stream = dt.state.itemCon[dt.state.delayed].stream;
				if(dt.state.curOrder.mClass != "Typesetting") {
					var imHeight = 100;
					if(dt.state.curOrder.parentStyle && dt.state.curOrder.parentStyle.height >= 100) {
						imHeight = dt.state.curOrder.parentStyle.height + 20;
					}
					item = {
						tName: "freeContainer",
						tText: "自由容器",
						isEdit: false,
						mClass: "Typesetting",
						isStreamColumn: false,
						isShowUp: false,
						isShowDown: false,
						conStyle: {
							width: '990px',
							margin: "0 auto",
							height: imHeight + 'px'
						},
						order: 1,
						itemInner: [dt.state.curOrder],
						cur: true
					}

				} else {
					item = dt.state.curOrder;
				}
				if(stream.length > 1) {
					stream.forEach(function(v, i) {
						console.log(i);
						if(i == 0) {
							v.isShowUp = false;
							v.isShowDown = true;
						} else {
							v.isShowUp = true;
							v.isShowDown = true;
						}
					});
					item.isShowUp = true;
					item.isShowDown = false;
				}
				item = JSON.parse(JSON.stringify(item));
				stream.push(item);
				dt.state.curOrder = null;
			},
			removeComponent(comp) {
				var stream = this.state.itemCon[this.state.delayed].stream;
				for(var j = 0; stream.length > j; j++) {
					var itemInner = stream[j].itemInner;
					if(itemInner && itemInner.length) {
						itemInner.splice(itemInner.indexOf(comp), 1);
					}

				}
			},
			setUpDown(i, flag) {
				var stream = this.state.itemCon[this.state.delayed].stream;
				var k = stream.indexOf(i);
				//console.log(k+flag)
				var obj = {
					k: k,
					j: k + flag
				}
				this.change(obj);

				stream.forEach(function(v, i) {
					if(i == 0) {
						v.isShowUp = false;
						v.isShowDown = true;
						console.log("ddt");
					} else if(i == stream.length - 1) {
						v.isShowUp = true;
						v.isShowDown = false;
						console.log("ddtsssss");
					} else {
						v.isShowUp = true;
						v.isShowDown = true;
					}
				});
				//item.isShowUp = true;
				//item.isShowDown = false;
			},
			delModule(item) {
				var stream = this.state.itemCon[this.state.delayed].stream;
				stream.splice(stream.indexOf(item), 1);
				this.orderbyId();
			},
			inAddNewOver(event) {
				event.preventDefault();
				console.log("进入新增容器(移动阶段 )");
			},
			...mapActions([
				"getData"
			]),
			...mapMutations([
				'orderbyId', 'setDelayed', "change"
			])
		},
		computed: {
			...mapGetters(["state"])
		},
		watch: {

		}
	}
</script>

