<template>
	<div class="buttonEdit">
		<div class="name">
			<span>按钮名称：</span>
			<input type="text" value="itemMdata.tText" v-model="itemMdata.tText" />
		</div>
		<div class="style">
			<p>样式：</p>
			<p class="default" @click="defaultshow">
				<input type="radio" name="radio" id="default" checked="checked" />
				<label for="default">默认</label>
			</p>
			<p class="custom" @click="customshow">
				<input type="radio" name="radio" id="custom" />
				<label for="custom">自定义</label>
			</p>
		</div>
		<div class="content">
			<div class="default_detail" v-show="default_show">
				<div class="look">
					<p>按钮样式：</p>
					<ul>
						<li @click="changeone">
							<div class="look_one" :style="{background: color1}"></div>
						</li>
						<li @click="changetwo">
							<div class="look_two" :style="{background: color1}"></div>
						</li>
						<li @click="changethree">
							<div class="look_three" :style="{['border-color']: color1}"></div>
						</li>
						<li @click="changefour">
							<div class="look_four" :style="{background: gradient}"></div>
						</li>
						<li @click="changefive">
							<div class="look_five" :style="{background: gradient}"></div>
						</li>
					</ul>
				</div>
				<div class="color">
					<p>按钮顶部颜色：</p>
					<div class="block">
						<el-color-picker show-alpha color-format="rgb" v-model="color2"></el-color-picker>
					</div>
				</div>
				<div class="color">
					<p>按钮底部颜色：</p>
					<div class="block">
						<el-color-picker show-alpha color-format="rgb" v-model="color1"></el-color-picker>
					</div>
				</div>
			</div>
			<div class="custom_detail" v-show="custom_show">
				<!--<div class="color">
					<p>按钮颜色：</p>
					<div class="block">
						<el-color-picker v-model="color1"></el-color-picker>
					</div>
				</div>-->
				<div class="img">
					<p>背景图片：</p>
					<button  @click="vbgFun(1)">添加图片</button>
					<img :src="itemMdata.mImage0.url" />
				</div>
			</div>
		</div>
		<el-dialog title="添加图片" :visible.sync="addVisibleNav" width="60%">
			<div class="g-cf add_nav_cons">
				<div class="list_con">
					<mImageEdit v-if="flag==1" :itemMdata="itemMdata.mImage0"></mImageEdit>
				</div>
			</div>
			<span slot="footer" class="dialog-footer">
		    <el-button @click="addVisibleNav = false">取 消</el-button>
		    <el-button type="primary" @click="addVisibleFun">保 存</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import { mapState, mapMutations, mapGetters, mapActions } from 'vuex'
	import mImageEdit from './mImageEdit.vue'
	export default {
		name: 'buttonEdit',
		created() {
			console.log(this.itemMdata);
		},
		components: {
			mImageEdit
		},
		props: ['itemMdata'],
		data() {
			return {
				title: '按钮编辑',
				default_show: true,
				custom_show: false,
				fileList2: [{
					name: 'background.jpeg',
					url: '/static/img/default.42302a3.png'
				}],
				color1: '#409EFF',
				color2: '#409EFF',
				addVisibleNav: false,
				flag:1
			}
		},
		computed: {
			// 计算属性的 getter
			gradient: function() {
				// `this` 指向 vm 实例
				return '-webkit-linear-gradient(bottom,'+this.color1+' 10%, '+this.color2+')'
			}
		},
		methods: {
			vbgFun(flag) {
				this.flag = flag;
				this.addVisibleNav = true;
				//console.log(flag)
			},
			addVisibleFun() {
				this.addVisibleNav = false;
			},
			defaultshow: function() {
				this.default_show = true;
				this.custom_show = false;
			},
			customshow: function() {
				this.custom_show = true;
				this.default_show = false;
			},
			changeone: function() {
				this.itemMdata.buttonStyle.borderRadius = '10px';
				this.itemMdata.buttonStyle.color = '#fff';
				this.itemMdata.buttonStyle.background = this.color1;
				this.itemMdata.buttonStyle.boxShadow = '';
			},
			changetwo: function() {
				this.itemMdata.buttonStyle.borderRadius = '0px';
				this.itemMdata.buttonStyle.color = '#fff';
				this.itemMdata.buttonStyle.background = this.color1;
				this.itemMdata.buttonStyle.boxShadow = '';
			},
			changethree: function() {
				this.itemMdata.buttonStyle.borderRadius = '0px';
				this.itemMdata.buttonStyle.color = '#000';
				this.itemMdata.buttonStyle.background = '#fff';
				this.itemMdata.buttonStyle.borderColor = this.color1;
				this.itemMdata.buttonStyle.boxShadow = '';
			},
			changefour: function() {
				this.itemMdata.buttonStyle.borderRadius = '10px';
				this.itemMdata.buttonStyle.color = '#fff';
				this.itemMdata.buttonStyle.borderColor = this.color1;
				this.itemMdata.buttonStyle.background = this.gradient;
				this.itemMdata.buttonStyle.boxShadow = '';
			},
			changefive: function() {
				this.itemMdata.buttonStyle.borderRadius = '10px';
				this.itemMdata.buttonStyle.color = '#fff';
				this.itemMdata.buttonStyle.background = this.gradient;
				this.itemMdata.buttonStyle.borderColor = this.color1;
				this.itemMdata.buttonStyle.boxShadow = '0 2px 5px 2px rgba(0, 0, 0, 0.2)';
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	.buttonEdit {
		text-align: left;
		padding: 30px;
		height: 480px;
		padding-bottom: 0;
		.name {
			input {
				border: 1px solid #E3E3E3;
				width: 180px;
				line-height: 30px;
				padding-left: 10px;
			}
		}
		.style {
			clear: both;
			overflow: hidden;
			margin-top: 10px;
			p {
				float: left;
				margin-right: 15px;
			}
		}
		.content {
			.default_detail {
				.look {
					clear: both;
					overflow: hidden;
					margin-bottom: 20px;
					p {
						float: left;
					}
					ul {
						width: 85%;
						margin-left: 10px;
						float: left;
						li {
							float: left;
							width: 150px;
							height: 110px;
							border: 1px solid #E3E3E3;
							margin-right: 15px;
							margin-top: 15px;
							.look_one {
								width: 70%;
								margin-left: 15%;
								margin-top: 35px;
								height: 40px;
								border-radius: 5px;
							}
							.look_two {
								width: 70%;
								margin-left: 15%;
								margin-top: 35px;
								height: 40px;
							}
							.look_three {
								border-style: solid;
								border-width: 1px;
								width: 70%;
								margin-left: 15%;
								margin-top: 35px;
								height: 40px;
							}
							.look_four {
								/*FILTER: progid:DXImageTransform.Microsoft.Gradient(gradientType=0, startColorStr=#409eff, endColorStr=#1b70c7);
								background: -moz-linear-gradient(top, #409eff, #1b70c7);
								background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#409eff), to(#1b70c7));*/
								width: 70%;
								margin-left: 15%;
								margin-top: 35px;
								height: 40px;
								border-radius: 5px;
							}
							.look_five {
								/*FILTER: progid:DXImageTransform.Microsoft.Gradient(gradientType=0, startColorStr=#409eff, endColorStr=#1b70c7);
								background: -moz-linear-gradient(top, #409eff, #1b70c7);
								background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#409eff), to(#1b70c7));*/
								width: 70%;
								margin-left: 15%;
								margin-top: 35px;
								height: 40px;
								border-radius: 5px;
								box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.2);
							}
						}
						li:hover {
							border: 1px solid #409eff;
						}
					}
				}
				.color {
					clear: both;
					overflow: hidden;
					margin-top: 10px;
					line-height: 30px;
					p {
						float: left;
					}
					.block {
						float: left;
						margin-left: 15px;
					}
				}
			}
			.custom_detail {
				.color {
					clear: both;
					overflow: hidden;
					margin-top: 20px;
					p {
						float: left;
					}
				}
				.img {
					clear: both;
					overflow: hidden;
					margin-top: 10px;
					p {
						float: left;
					}
					button{
						background: #409EFF;
						border-radius: 5px;
						width: 100px;
						line-height: 30px;
						text-align: center;
						color: #FFFFFF;
					}
					img{
						margin-top: 10px;
						width: 120px;
						margin-left: 60px;
					}
				}
			}
		}
	}
	
	.el-upload-list__item {
		width: 50% !important;
	}
</style>


