const mutations = {
	setState(state, data) {
		//console.log(data);
		state.itemCon = data.itemCon; //
	},
	setDelayed(state, id) {
		state.delayed = id; //
	},
	setOrderId(state, id) {
		state.id = id; //
	},
	orderbyId(state) {
		var stream = state.itemCon[state.delayed].stream;
		stream = stream.map((obj, index) => {
			obj.order = index + 1;
			return obj;
		});
	},
	change(state, k, j) {
		var arr = state.itemCon[state.delayed].stream;		 
		var tempOption = arr[k.j];  
		Vue.set(arr, k.j, arr[k.k]);  
		Vue.set(arr, k.k, tempOption); 
	},
	pushItem(state, item) {
		state.itemCon[state.delayed].push(item); //追加组件
	},
	modifyItem(state, i, item) {
		state.itemCon[state.delayed][i] = item; //拖动排序
	},
	modifyStyle(state, i, style) {
		state.itemCon[i].style = style; //修改组件样式
	},
	previewData(state, id) {
		localStorage.setItem(id, JSON.stringify(state));
	},
	setCurOrder(state, order) {
		state.curOrder = order; //正在的拖动组件（临时保存）
	}
}
